/**
 * Google Map Info Box
 *
 */

.infoBox {

	& > img {
		background: rgba(#fff, 1);
		transition: 0.2s all;

		&:hover {
			background: rgba(#fff, 0.8);
		}
	}

	.map-info-window {
	    display: block;
	    background: #fff;
	    margin-bottom: 2rem;
	    border-bottom: 3px solid $rh_primary_color;

	    .prop-thumb {
	    	width: 100%;
	    }

	    .prop-title {
	    	color: $rh_heading_color;
	    	font-weight: 500;
		    font-size: 1.5rem;
		    margin: 0;
		    padding: 1.5rem;
		    line-height: 1.5;
	    }

	    a {
	    	transition: 0.2 all;
	    }

	    p {
	    	padding: 0 1.5rem 1.5rem;
	    	color: $rh_primary_color;
	    }

	    .arrow-down {
	    	width: 0;
		    height: 0;
		    border-style: solid;
		    border-width: 10px 10px 0 10px;
		    border-color: $rh_primary_color transparent transparent transparent;
		    position: absolute;
		    bottom: 1rem;
		    left: 50%;
		    left: calc(50% - 5px);
	    }
	}
}
