/**
 * Blog Template
 */

.rh_blog__post {
	display: flex;
	flex-direction: column;
	background: #ffffff;
	text-align: left;
	margin: 0 0 3rem 0;

	figure {
		display: block;
		margin: 0;

		img {
			display: block;
			max-width: 100%;
			margin: 0 auto;
		}
	}

	.entry-header {
		display: block;
	    position: relative;
		z-index: 9;
		margin: 0 3rem;
		margin-top: -9rem;
		padding: 2rem 2.5rem;
		background: $rh_primary_color;

		@include r(320)  { margin: 0; margin-top: 0; }
		@include r(480)  { margin: 0; margin-top: 0; }
		@include r(768)  { margin: 0 3rem; margin-top: -9rem; }
		@include r(1024) { margin: 0 3rem; margin-top: -9rem; }
		@include r(1140) { margin: 0 3rem; margin-top: -9rem; }
		@include r(1280) { margin: 0 3rem; margin-top: -9rem; }

		.entry-title {
			margin: 0;
			margin-bottom: 2rem;
			font-size: 2.1rem;
			font-weight: 500;
			line-height: 1.5;
			color: #fff;

			a {
				display: inline-block;
				color: #fff;
				font-size: 2.1rem;
				font-weight: 500;
				line-height: 1.5;
				transition: 0.2s all linear;

				&:hover {
					color: darken(#fff, 10%);
					transition: 0.2s all linear;
				}
			}
		}

		.entry-meta {
			color: $rh_option_active;
			font: 400 1.5rem/2 $rubikFontFamily;

			.vcard {
				display: inline-block;
				color: $rh_option_active;
			}

			a {
				color: $rh_option_active;
				transition: 0.2s all linear;

				&:hover {
					color: #fff;
					transition: 0.2s all linear;
				}
			}
		}
	}

	.entry-summary {
		padding: 3rem;
		background: #fff;
		margin-top: 0;

		@include r(320)  { padding: 2rem 2.5rem 2.5rem 2.5rem; }
		@include r(480)  { padding: 2rem 2.5rem 2.5rem 2.5rem; }
		@include r(768)  { padding: 3rem; }
		@include r(1024) { padding: 3rem; }
		@include r(1140) { padding: 3rem; }
		@include r(1280) { padding: 3rem; }

		p {
			line-height: 2;
			margin-bottom: 1.5rem;
		}
	}

	.post-video {
		.video-wrapper {
			iframe {
				display: block;
				width: 100%;
				height: 450px;
			}
		}
	}

	.listing-slider {
		position: relative;
		background: #fff;

		&:hover {
			.flex-direction-nav {
				.flex-prev {
					left: 2rem;
					opacity: 1;
				}
				.flex-next {
					right: 2rem;
					opacity: 1;
				}
			}
		}

		.flex-control-nav {
			bottom: 100px;

			@include r(320)  { bottom: 15px; }
			@include r(480)  { bottom: 15px; }
			@include r(768)  { bottom: 100px; }
			@include r(1024) { bottom: 100px; }
			@include r(1140) { bottom: 100px; }
			@include r(1280) { bottom: 100px; }
		}

		.flex-direction-nav {
			a {
			    width: 45px;
				height: 45px;
			}
			.flex-prev {
				left: -2rem;
				opacity: 0;
			}
			.flex-next {
				right: -2rem;
				opacity: 0;
			}
		}

		img {
			display: block;
			margin: 0 auto;
		}
	}
}

.rh_blog__single {
	background: #fff;
}

.entry-header-margin-fix {
	margin-top: 12rem;

	@include r(320)  { margin-top: 0; }
	@include r(480)  { margin-top: 0; }
	@include r(768)  { margin-top: 12rem; }
	@include r(1024) { margin-top: 12rem; }
	@include r(1140) { margin-top: 12rem; }
	@include r(1280) { margin-top: 12rem; }

	&:first-of-type {
		margin-top: 9rem;

		@include r(320)  { margin-top: 0; }
		@include r(480)  { margin-top: 0; }
		@include r(768)  { margin-top: 9rem; }
		@include r(1024) { margin-top: 9rem; }
		@include r(1140) { margin-top: 9rem; }
		@include r(1280) { margin-top: 9rem; }
	}
}

// Single Post Content

.entry-content {
	padding: 2rem 3rem;
	margin: 0 2rem;

	@include r(320)  { padding: 2rem 2.5rem; margin: 0; }
	@include r(480)  { padding: 2rem 2.5rem; margin: 0; }
	@include r(768)  { padding: 2rem 3rem; margin: 0 2rem; }
	@include r(1024) { padding: 2rem 3rem; margin: 0 2rem; }
	@include r(1140) { padding: 2rem 3rem; margin: 0 2rem; }
	@include r(1280) { padding: 2rem 3rem; margin: 0 2rem; }
}

.rh_blog__single {
	.post_format-post-format-video {
		.entry-header {
			margin-top: 0;
		}
	}
}

// Page
.page {
	.entry-header {
		min-height: 12rem;

		.entry-title {
			margin-bottom: 0;
		}
	}
}
