/**
 * Homepage sections
 */

.rh_section {
	display: block;
	text-align: center;

	.rh_section__head {
		max-width: 1440px;
    	margin: 0 auto;

		.rh_section__title {
			margin: 0;
			font-weight: 300;
			padding: 0 0 1.5rem 0;

			span {
				display: block;
				color: $rh_primary_color;
				font-size: 1.5rem;
				font-weight: 500;
			}
		}

		.rh_section__desc {
			font-size: 1.5rem;
			line-height: 1.5;
		}
	}
}

.rh_section__properties {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	position: relative;
	z-index: 9;
	max-width: 1440px;
    margin: 0 auto;

    @include r(320)  { padding: 0 0.5rem; }
	@include r(480)  { padding: 0 1.5rem; }
	@include r(768)  { padding: 0 3.5rem; }
	@include r(1024) { padding: 0 3.5rem; }
	@include r(1140) { padding: 0 5.5rem; }
	@include r(1280) { padding: 0 8.5rem; }
}

.rh_section--flex {
	display: flex;
	flex-wrap: nowrap;

	@include r(320)  { flex-wrap: wrap; }
	@include r(480)  { flex-wrap: wrap; }
	@include r(768)  { flex-wrap: wrap; }
	@include r(1024) { flex-wrap: nowrap; }
	@include r(1140) { flex-wrap: nowrap; }
	@include r(1280) { flex-wrap: nowrap; }
}

.rh_section--props_padding {
	position: relative;

	@include r(320)  { padding: 3rem 0 0; }
	@include r(480)  { padding: 3rem 0 0; }
	@include r(768)  { padding: 5rem 0 3rem 0; }
	@include r(1024) { padding: 5rem 0 7rem 0; }
	@include r(1140) { padding: 7rem 0; }
	@include r(1280) { padding: 10rem 0 7rem 0; }

	&:after {
		content: '';
		color: transparent;
	    width: 0;
	    height: 0;
        border-bottom-width: 180px;
	    border-left: 1440px solid $rh_body_bg;
	    border-left: 100vw solid $rh_body_bg;
	    position: absolute;
	    bottom: -90px;
	    left: 0;
	    z-index: 9999;

	    @include r(320)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(480)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(768)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(1024) { bottom: -70px; border-left-width: 100vw; border-bottom-width: 140px; }
		@include r(1140) { bottom: -80px; border-left-width: 100vw; border-bottom-width: 160px; }
		@include r(1280) { bottom: -90px; border-left-width: 100vw; border-bottom-width: 180px; }
	}

	.rh_section__head {
		@include r(320)  { padding: 0 2rem 3rem; }
		@include r(480)  { padding: 0 3rem 3rem; }
		@include r(768)  { padding: 0 5rem 5rem; }
		@include r(1024) { padding: 0 5rem 5rem; }
		@include r(1140) { padding: 0 7rem 7rem; }
		@include r(1280) { padding: 0 10rem 7rem; }
	}
}

.rh_section--top_padding {
	@include r(320)  { padding-top: 2rem; }
	@include r(480)  { padding-top: 3rem; }
	@include r(768)  { padding-top: 5rem; }
	@include r(1024) { padding-top: 5rem; }
	@include r(1140) { padding-top: 7rem; }
	@include r(1280) { padding-top: 10rem; }
}

.rh_section--featured {
	padding: 0 10rem 7.5rem 10rem;
	background: #fff;
	position: relative;

	@include r(320)  { padding: 3rem 0 1.5rem; }
	@include r(480)  { padding: 3rem 0 0 0; }
	@include r(768)  { padding: 5rem 0 3rem 0; }
	@include r(1024) { padding: 9rem 0 5rem 0; }
	@include r(1140) { padding: 9rem 0; }
	@include r(1280) { padding: 10rem 0 7rem 0; }

	&:before {
		content: '';
		color: transparent;
	    width: 0;
	    height: 0;
	    border: 0 solid transparent;
	    border-right-width: 0px;
	    border-left-width: 1440px;
	    border-left-width: 100vw;
	    border-bottom: 180px solid #fff;
	    position: absolute;
	    top: -90px;
	    left: 0;
	    z-index: 9999;

	    @include r(320)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(480)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(768)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(1024) { top: -70px; border-left-width: 100vw; border-bottom-width: 140px; }
		@include r(1140) { top: -80px; border-left-width: 100vw; border-bottom-width: 160px; }
		@include r(1280) { top: -90px; border-left-width: 100vw; border-bottom-width: 180px; }
	}

	&:after {
		content: '';
		color: transparent;
	    width: 0;
	    height: 0;
	    border: 0 solid transparent;
        border-bottom-width: 180px;
	    border-left: 1440px solid #fff;
	    border-left: 100vw solid #fff;
	    position: absolute;
	    bottom: -90px;
	    left: 0;
	    z-index: 9999;

	    @include r(320)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(480)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(768)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(1024) { bottom: -70px; border-left-width: 100vw; border-bottom-width: 140px; }
		@include r(1140) { bottom: -80px; border-left-width: 100vw; border-bottom-width: 160px; }
		@include r(1280) { bottom: -90px; border-left-width: 100vw; border-bottom-width: 180px; }
	}

	.rh_section__head {
		@include r(320)  { padding: 0 2rem 3rem; }
		@include r(480)  { padding: 0 3rem 3rem; }
		@include r(768)  { padding: 0 5rem 5rem; }
		@include r(1024) { padding: 3rem 5rem 5rem; }
		@include r(1140) { padding: 3rem 7rem 7rem; }
		@include r(1280) { padding: 3rem 10rem 7rem; }
	}
}

.rh_section__map_listing {
	@include r(320)  { flex-wrap: wrap; margin-top: 2rem; }
	@include r(480)  { flex-wrap: wrap; margin-top: 3rem; }
	@include r(768)  { flex-wrap: wrap; margin-top: 5rem; }
	@include r(1024) { flex-wrap: wrap; margin-top: -4.5rem; }
	@include r(1140) { flex-wrap: nowrap; margin-top: -4rem; }
	@include r(1280) { flex-wrap: nowrap; margin-top: -4.75rem; }
}

.rh_section__left_sidebar {
	.rh_page__sidebar {
		margin-right: 3rem;

		@include r(320)  { order: 2; margin: 0 auto; }
		@include r(480)  { order: 2; margin: 0 auto; }
		@include r(768)  { order: 2; margin: 0 auto; }
		@include r(1024) { order: 1; margin-right: 3rem; }
		@include r(1140) { order: 1; margin-right: 3rem; }
		@include r(1280) { order: 1; margin-right: 3rem; }
	}

	.rh_page__main {
		padding-right: 0;

		@include r(320)  { order: 1; }
		@include r(480)  { order: 1; }
		@include r(768)  { order: 1; }
		@include r(1024) { order: 2; }
		@include r(1140) { order: 2; }
		@include r(1280) { order: 2; }
	}
}
