/**
 * Form Styles
 */

.rh_form {
	display: block;
	position: relative;
	padding: 3rem 1.5rem;
	background: #fff;
	text-align: left;

	@include r(320)  { padding: 2rem 1.5rem; margin-bottom: 2rem; margin-top: 1.5rem; }
	@include r(480)  { padding: 3rem 1.5rem; margin-bottom: 3rem; margin-top: 1.5rem; }
	@include r(768)  { padding: 3rem 1.5rem; margin-bottom: 5rem; margin-top: 1.5rem; }
	@include r(1024) { padding: 3rem 1.5rem; margin-bottom: 5rem; margin-top: 0; }
	@include r(1140) { padding: 3rem 1.5rem; margin-bottom: 0; margin-top: 0; }
	@include r(1280) { padding: 3rem 1.5rem; margin-bottom: 0; margin-top: 0; }

	#rh_modal__forgot_form {
		display: none;
	}
}

.rh_form__login_wrap {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	flex-direction: row;

	@include r(320)  { flex-direction: column; }
	@include r(480)  { flex-direction: column; }
	@include r(768)  { flex-direction: column; }
	@include r(1024) { flex-direction: row; }
	@include r(1140) { flex-direction: row; }
	@include r(1280) { flex-direction: row; }

	.rh_form__login {
		width: 50%;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 100%; }
		@include r(1024) { width: 50%; }
		@include r(1140) { width: 50%; }
		@include r(1280) { width: 50%; }
	}

	.rh_form__register {
		width: 50%;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 100%; }
		@include r(1024) { width: 50%; }
		@include r(1140) { width: 50%; }
		@include r(1280) { width: 50%; }
	}
}

.rh_form__row {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	padding: 0;
}

.rh_form__item {
	display: flex;
	flex: 0 1 auto;
	padding: 0 1.5rem 3rem;

	@include r(320)  { padding: 0 0.5rem 2rem; }
	@include r(480)  { padding: 0 1.5rem 3rem; }
	@include r(768)  { padding: 0 1.5rem 3rem; }
	@include r(1024) { padding: 0 1.5rem 3rem; }
	@include r(1140) { padding: 0 1.5rem 3rem; }
	@include r(1280) { padding: 0 1.5rem 3rem; }

	label {
		display: inline-block;
		margin-bottom: 1rem;
		font-size: 1.5rem;
		font-weight: 500;
		line-height: 1.5;
		padding: 0;
	}

	.error {
		margin: 1rem 0 0 0;
		font-weight: 400;
		color: #ea4d4d;
	}

	input {
		display: block;
		width: 100%;
		margin: 0 auto;
		padding: 1.3rem 1.5rem;
		border: 1px solid rgba($rh_text_color, 0.2);
		font-size: 1.5rem;
		color: $rh_text_color;
	}

	input[type="submit"] {
		display: inline-block;
		width: initial;
		margin: 0;
		color: #fff;
		cursor: pointer;
	}

	textarea {
		display: block;
		width: 100%;
		margin: 0 auto;
		padding: 1.3rem 1.5rem;
		border: 1px solid rgba($rh_text_color, 0.2);
		font-size: 1.5rem;
		line-height: 2;
		color: $rh_text_color;
	}

	p.note {
		font-size: 1.3rem;
		font-weight: 500;
		line-height: 1.5;
		margin-top: 1rem;
	}

	.rh_form__social {
		display: flex;

		span {
			width: 46px;
			height: 46px;
			padding: 1.5rem;
			font-size: 1.7rem;
			line-height: 0.6;
			border: 1px solid rgba($rh_text_color, 0.2);
			border-right: none;
			color: rgba($rh_text_color, 0.5);
		}

		input {
			border-left: none;
			padding: 1.3rem 1.5rem 1.3rem 0;
		}
	}

	.wp-editor-container {
		border: 1px solid rgba($rh_text_color, 0.2);
	}

	.quicktags-toolbar input {
		display: inline-block;
		width: initial;
	}

	.selectwrap {
		select {
			outline: 0;
		}
	}

	span {
		border: none;
	}

	.select2-container {
		display: block;
		outline: 0;

		&:focus {
			outline: 0;
		}
	}

	.select2-selection--single {
		border: 1px solid rgba($rh_text_color, 0.2);
		border-radius: 0;
		height: initial;
		outline: none;

		.select2-selection__rendered {
			color: $rh_text_color;
			font-size: 1.5rem;
			line-height: 1.2;
			padding: 1.3rem 0 1.3rem 1.5rem;
		}

		.select2-selection__arrow {
			width: 44px;
			height: 44px;
			background: $rh_body_bg;
			border-left: 1px solid rgba($rh_text_color, 0.2);

			b {
				right: 1.7rem;
			}
		}
	}

	#gallery-thumbs-container {
		display: flex;
		flex-wrap: wrap;

		.gallery-thumb {
			display: flex;
			align-items: center;
			justify-content: center;
			background: $rh_body_bg;
			position: relative;
			margin: 0 1.5rem 1.5rem 0;
			width: 150px;
			height: 150px;
			font-size: 1.4rem;
			color: $rh_text_color;

			.mark-featured {
				position: absolute;
				bottom: 0;
				left: 0;
			}

			.remove-image {
				position: absolute;
				bottom: 0;
				right: 0;
			}

			a {
				background: rgba(#000, 0.5);
				color: #fff;
				padding: 0.5rem 0.8rem;
			}

			.loader {
				display: none;
				position: absolute;
				top: 0;
				left: 0;
				background: rgba(#000, 0.5);
				color: #fff;
				padding: 0.5rem 0.8rem;
			}
		}
	}

	#drag-and-drop {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 30rem;
		background: $rh_body_bg;
		border: 1px solid rgba($rh_text_color, 0.2);
		border-style: dashed;
		font: 500 1.5rem/1.5 $rubikFontFamily;

		@include r(320)  { flex-direction: column; }
		@include r(480)  { flex-direction: column; }
		@include r(768)  { flex-direction: column; }
		@include r(1024) { flex-direction: column; }
		@include r(1140) { flex-direction: row; }
		@include r(1280) { flex-direction: row; }

		div {
			display: flex;
			margin: 0 1rem;

			@include r(320)  { margin: 1rem 0; }
			@include r(480)  { margin: 1rem 0; }
			@include r(768)  { margin: 1rem 0; }
			@include r(1024) { margin: 1rem 0; }
			@include r(1140) { margin: 0 1rem; }
			@include r(1280) { margin: 0 1rem; }
		}

		i {
			font-size: 2.2rem;
			color: rgba(#000, 0.2);
		}
	}

	.field-description {
		display: flex;
		align-items: flex-start;
		margin: 1.5rem 0;

		span {
			font-weight: 500;
			font-size: 1.3rem;
			line-height: 1.5;
		}
		.note {
			width: 6rem;

			@include r(320)  { width: 13rem; }
			@include r(480)  { width: 9rem; }
			@include r(768)  { width: 9rem; }
			@include r(1024) { width: 8rem; }
			@include r(1140) { width: 7rem; }
			@include r(1280) { width: 6rem; }
		}
	}

	.address-wrapper {
		padding: 2.3rem;
		z-index: 999;

		@include r(320)  { padding: 2rem; }
		@include r(480)  { padding: 2.3rem; }
		@include r(768)  { padding: 2.3rem; }
		@include r(1024) { padding: 2.3rem; }
		@include r(1140) { padding: 2.3rem; }
		@include r(1280) { padding: 2.3rem; }

		input {
			background: #fff;
			margin: 0 0 1rem 0;
		}

		#address-error {
			float: none;
			width: 100%;
			text-align: right;

			@include r(320)  { float: right; width: 40%; }
			@include r(480)  { float: right; width: 50%; }
			@include r(768)  { float: none; width: 100%; }
			@include r(1024) { float: none; width: 100%; }
			@include r(1140) { float: none; width: 100%; }
			@include r(1280) { float: none; width: 100%; }
		}
	}

	.map-wrapper {
		height: 40rem;
	    position: absolute;
	    top: 0;
	    display: block;
	    width: 100%;
	    padding: 0 3rem 0 0;

	    @include r(320)  { padding: 0 1rem 0 0; }
		@include r(480)  { padding: 0 3rem 0 0; }
		@include r(768)  { padding: 0 3rem 0 0; }
		@include r(1024) { padding: 0 3rem 0 0; }
		@include r(1140) { padding: 0 3rem 0 0; }
		@include r(1280) { padding: 0 3rem 0 0; }

		.map-canvas {
			height: 100%;
		}

		.rh_btn {
			position: absolute;
		    z-index: 999;
		    left: 2.3rem;
		    top: 12rem;
		}
	}

	#inspiry-additional-details-container {
		.inspiry-detail {
			display: flex;
			align-items: center;
			margin: 1rem 0;

			@include r(320)  { flex-direction: column; }
			@include r(480)  { flex-direction: row; }
			@include r(768)  { flex-direction: row; }
			@include r(1024) { flex-direction: row; }
			@include r(1140) { flex-direction: row; }
			@include r(1280) { flex-direction: row; }

			.inspiry-detail-control {
				display: flex;
				align-items: center;
				height: 46px;

				i {
					font-size: 2.2rem;
					color: rgba($rh_text_color, 0.5);
					transition: 0.2s all linear;
				}

				.sort-detail {
					cursor: pointer;

					&:hover {
						color: #2196f3;
						transition: 0.2s all linear;
					}
				}

				.remove-detail {
					i {
						&:hover {
							color: #d32c3d;
							transition: 0.2s all linear;
						}
					}
				}
			}

			.inspiry-detail-title,
			.inspiry-detail-value {
				flex: 1 1 auto;
			}

			.inspiry-detail-title {
				margin: 0 1.5rem;

				@include r(320)  { margin: 0.5rem 0; width: 100%; }
				@include r(480)  { margin: 0 0.5rem 0 1rem; width: initial; }
				@include r(768)  { margin: 0 1.5rem; width: initial; }
				@include r(1024) { margin: 0 1.5rem; width: initial; }
				@include r(1140) { margin: 0 1.5rem; width: initial; }
				@include r(1280) { margin: 0 1.5rem; width: initial; }
			}

			.inspiry-detail-value {
				margin: 0 1.5rem;

				@include r(320)  { margin: 0.5rem 0; width: 100%; }
				@include r(480)  { margin: 0 1rem 0 0.5rem; width: initial; }
				@include r(768)  { margin: 0 1.5rem; width: initial; }
				@include r(1024) { margin: 0 1.5rem; width: initial; }
				@include r(1140) { margin: 0 1.5rem; width: initial; }
				@include r(1280) { margin: 0 1.5rem; width: initial; }
			}
		}
	}

	.inspiry-details-wrapper .inspiry-detail {
		display: inline-block;
		margin: 0 3.5rem;
		text-align: center;

		@include r(320)  { display: block; margin: 0 auto; }
		@include r(480)  { display: inline-block; margin: 0 3rem; }
		@include r(768)  { display: inline-block; margin: 0 3.5rem; }
		@include r(1024) { display: inline-block; margin: 0 3.5rem; }
		@include r(1140) { display: inline-block; margin: 0 3.5rem; }
		@include r(1280) { display: inline-block; margin: 0 3.5rem; }

		.inspiry-detail-control {
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.add-detail {
			display: flex;
			align-items: center;
			width: 37px;
			height: 37px;
			background: $rh_secondary_color;
			color: #fff;
			padding: 1rem 1.3rem;
			transition: 0.2s all linear;

			&:hover {
				background: darken($rh_secondary_color, 7%);
				transition: 0.2s all linear;
			}
		}
	}

	.features-checkboxes {
		display: flex;
		flex-wrap: wrap;
		list-style-type: none;

		li {
			width: 25%;

			@include r(320)  { width: 100%; }
			@include r(480)  { width: 100%; }
			@include r(768)  { width: 50%; }
			@include r(1024) { width: 25%; }
			@include r(1140) { width: 25%; }
			@include r(1280) { width: 25%; }
		}
	}

	#errors-log {
		color: $rh_text_color;
		font: 400 1.4rem/1.5 $rubikFontFamily;
	}

	// MultiSelect styles - Since version 3.1.0
	.select2-container--default.select2-container--focus .select2-selection--multiple,
	.select2-container--default .select2-selection--multiple {
		border-color: #cdcdcd;
		box-shadow: none;
		min-height: 38px;
		border-radius: 0;
	}

	.select2-container--default .select2-selection--multiple .select2-selection__choice {
		background-color: #f7f7f7;
		height: 28px;
		line-height: 27px;
		border-radius: 0;
	}
}

.rh_form__response {
	padding-bottom: 0;

	.rh_form__msg {
		display: none;
		margin-bottom: 3rem;
	}

	.rh_form__error {
		display: none;
		margin-bottom: 3rem;
	}
}

.address-map-fields-wrapper {
	height: 43rem;
}

.rh_form__submit {
	align-items: center;
}

.rh_form--columnAlign {
	flex-direction: column;
}

.rh_form--3-column {
	width: 33.3%;

	@include r(320)  { width: 100%; }
	@include r(480)  { width: 100%; }
	@include r(768)  { width: 50%; }
	@include r(1024) { width: 33.3%; }
	@include r(1140) { width: 33.3%; }
	@include r(1280) { width: 33.3%; }
}

.rh_form--2-column {
	width: 50%;

	@include r(320)  { width: 100%; }
	@include r(480)  { width: 100%; }
	@include r(768)  { width: 100%; }
	@include r(1024) { width: 50%; }
	@include r(1140) { width: 50%; }
	@include r(1280) { width: 50%; }
}

.rh_form--1-column {
	width: 100%;
}

.rh_form--relative {
	position: relative;
}

.rh_form--align_start {
	justify-content: flex-start;
}

.rh_form--align_end {
	justify-content: flex-end;
}

.rh_form__user_profile {
	@include r(320)  { flex-direction: column; }
	@include r(480)  { flex-direction: column; }
	@include r(768)  { flex-direction: row; }
	@include r(1024) { flex-direction: row; }
	@include r(1140) { flex-direction: row; }
	@include r(1280) { flex-direction: row; }
}

.rh_checkbox {

	label {
		position: relative;
		padding: 0 0 0 4rem;
		margin-top: 0.5rem;
		margin-bottom: 0.5rem;
		cursor: pointer;
	}

	span.rh_checkbox__title {
		display: inline-block;
		padding: 0.6rem 0;
		font-size: 1.4rem;
		color: $rh_text_color;
	}

	input[type="checkbox"] {
		visibility: hidden;
		display: none;

		&:checked ~ .rh_checkbox__indicator {
			&:after {
				display: block;
			}
		}
	}

	.rh_checkbox__indicator {
		position: absolute;
		top: 0;
		left: 0;
		width: 25px;
		height: 25px;
		background: #fff;
		border: 1px solid rgba($rh_text_color, 0.2);
		-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.1);
		-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.1);
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

		&:after {
			content: '';
			position: absolute;
			display: none;
			width: 24px;
			height: 24px;
			background: url( '../../images/icons/icon-tick.png' ) 50% 50% no-repeat;
		}
	}

	#terms-error {
		padding: 0 0 0 1rem;
	}
}

.rh_checkbox__featured {
	display: flex;
	background: $rh_body_bg;
	padding: 1.5rem 2rem;

	label {
		margin: 0;
		padding: 0 0 0 4rem;

		@include r(320)  { padding: 0 0 0 3rem; }
		@include r(480)  { padding: 0 0 0 3rem; }
		@include r(768)  { padding: 0 0 0 4rem; }
		@include r(1024) { padding: 0 0 0 4rem; }
		@include r(1140) { padding: 0 0 0 4rem; }
		@include r(1280) { padding: 0 0 0 4rem; }

		input {
			display: inline-block;
			width: initial;
		}

		.rh_checkbox__title {
			font-weight: 500;
			margin-left: 1rem;
			padding: 0;
			line-height: 1.5;

			@include r(320)  { margin-left: 1rem; padding: 0; }
			@include r(480)  { margin-left: 1rem; padding: 0.2rem 0; }
			@include r(768)  { margin-left: 0; padding: 0.3rem 0; }
			@include r(1024) { margin-left: 0; padding: 0.3rem 0; }
			@include r(1140) { margin-left: 0; padding: 0.3rem 0; }
			@include r(1280) { margin-left: 0; padding: 0.3rem 0; }
		}
	}
}

.rh_agent_options {
	display: flex;
	flex-direction: column;

	input[type="radio"] {
		visibility: hidden;

		&:checked ~ .control__indicator {
			&:after {
				display: block;
			}
		}
	}

	input {
		width: initial;
	}

	label {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;
		position: relative;
		padding: 0 0 0 4rem;
		cursor: pointer;
		margin: 1.5rem 0;

		span {
			font-size: 1.4rem;
			line-height: 2;
			margin-right: 1rem;
		}

		a {
			font-size: 1.4rem;
			transition: 0.2s all linear;

			&:hover {
				transition: 0.2s all linear;
			}
		}

		.sub-title {
			color: $rh_text_color;
		}

		.control__indicator {
			position: absolute;
			top: 0;
			left: 0;
			width: 25px;
			height: 25px;
			background: $rh_body_bg;
			border: 1px solid rgba($rh_text_color, 0.2);
			border-radius: 500rem;
			-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.1);
			-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.1);
			box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

			&:after {
				content: '';
				position: absolute;
				display: none;
				width: 13px;
				height: 13px;
				border-radius: 500rem;
				background: $rh_secondary_color;
				top: 5px;
				left: 5px;
			}
		}
	}

	.rh_agent_options__wrap {
		display: flex;
		justify-content: flex-start;
		align-items: center;

		@include r(320)  { flex-direction: column; align-items: flex-start; }
		@include r(480)  { flex-direction: column; align-items: flex-start; }
		@include r(768)  { flex-direction: row; align-items: center; }
		@include r(1024) { flex-direction: row; align-items: center; }
		@include r(1140) { flex-direction: row; align-items: center; }
		@include r(1280) { flex-direction: row; align-items: center; }

		span.select2 {
			width: 30rem;
			margin-left: 1rem;

			@include r(320)  { margin-left: 0; width: 100%; }
			@include r(480)  { margin-left: 0; width: 100%; }
			@include r(768)  { margin-left: 1rem; width: 30rem; }
			@include r(1024) { margin-left: 1rem; width: 30rem; }
			@include r(1140) { margin-left: 1rem; width: 30rem; }
			@include r(1280) { margin-left: 1rem; width: 30rem; }
		}
	}
}

// Property Address
.ui-autocomplete {
	width: 445px;
	padding: 0;
	margin: 0;
	list-style: none;
	z-index: 10000;
	border: 1px solid rgba($rh_text_color, 0.2);
	background: #fff;
}
.ui-autocomplete li {
	padding: 1rem;
	margin-bottom: 0;
	white-space: nowrap;
	text-align: left;
	cursor: pointer;

	&:hover {
		background: #f1f1f1;
		transition: 0.2s all linear;
	}
}
.ui-autocomplete li a {
	display: block;
	height: 100%;
}
.ui-autocomplete li a.ui-state-focus {
	cursor: pointer;
}
