/**
 * Theme Content
 */

.rh_content {
	color: $rh_text_color;
	font-size: 1.5rem;
	font-weight: 400;
	line-height: 2;
	text-align: left;

	p {
		font-size: 1.5rem;
		line-height: 2;
		margin-bottom: 2rem;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 0 auto;
		margin-bottom: 2rem;
		line-height: 1.5;
	}

	ol,
	ul {
		margin: 0;
		margin-bottom: 1rem;
		padding: 0 0 0 3rem;
		color: $rh_text_color;
		line-height: 2;
	}

	blockquote {
		position: relative;
	    margin: 40px 0;
	    padding: 34px 36px 46px 56px;

		&:before {
			color: #c6cdd1;
			content: "\f10d";
		    position: absolute;
		    top: 30px;
		    left: 28px;
		    display: inline-block;
		    font: normal normal normal 14px/1 FontAwesome;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -moz-osx-font-smoothing: grayscale;
		    -webkit-transform: translate(0, 0);
		    -ms-transform: translate(0, 0);
		    transform: translate(0, 0);
		    font-size: 24px;
		}

		p {
			font-size: 1.7rem;
	        margin: 0;
	    }
	}

	.error {
		background: #ff1200;
	}
}

.post-password-form {
	text-align: left;

	p {
		line-height: 2;
	}

	input {
		border-bottom: 1px solid rgba($rh_text_color, 0.2);
	}
	input[type="submit"] {
		@extend .rh_btn;
		@extend .rh_btn--primary;
		padding: 0.7rem 2rem;
	}
}

.info {
	background: $rh_footer_bg;
	color: #fff;
	padding: 1.5rem 3rem 1.5rem 2rem;
	position: relative;
	transition: 0.2s all linear;
}

.tip {
	background: $rh_secondary_color;
	color: #fff;
	padding: 1.5rem 3rem 1.5rem 2rem;
	position: relative;
	transition: 0.2s all linear;
}

.success {
	background: $rh_primary_color;
	color: #fff;
	padding: 1.5rem 3rem 1.5rem 2rem;
	position: relative;
	transition: 0.2s all linear;
}

.error {
	color: #fff;
	padding: 1.5rem 3rem 1.5rem 2rem;
	position: relative;
	transition: 0.2s all linear;
}

.icon-remove {
	display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
	font-size: 1.8rem;
	color: #fff;
	position: absolute;
	top: 2rem;
	right: 1.5rem;
	cursor: pointer;

	&:before {
		content: "\f00d";
	}
}

.row-fluid {
	display: flex;
	margin: 0 -1rem;

	@include r(320)  { flex-wrap: wrap; }
	@include r(480)  { flex-wrap: wrap; }
	@include r(768)  { flex-wrap: wrap; }
	@include r(1024) { flex-wrap: nowrap; }
	@include r(1140) { flex-wrap: nowrap; }
	@include r(1280) { flex-wrap: nowrap; }

	.span3 {
		flex: 1 1 25%;
		width: 25%;
		padding: 0 1rem;

		@include r(320)  { flex: 1 1 100%; width: 100%; }
		@include r(480)  { flex: 1 1 100%; width: 100%; }
		@include r(768)  { flex: 1 1 100%; width: 100%; }
		@include r(1024) { flex: 1 1 25%; width: 25%; }
		@include r(1140) { flex: 1 1 25%; width: 25%; }
		@include r(1280) { flex: 1 1 25%; width: 25%; }
	}

	.span6 {
		flex: 1 1 50%;
		width: 50%;
		padding: 0 1rem;

		@include r(320)  { flex: 1 1 100%; width: 100%; }
		@include r(480)  { flex: 1 1 100%; width: 100%; }
		@include r(768)  { flex: 1 1 100%; width: 100%; }
		@include r(1024) { flex: 1 1 50%; width: 50%; }
		@include r(1140) { flex: 1 1 50%; width: 50%; }
		@include r(1280) { flex: 1 1 50%; width: 50%; }
	}

	.span4 {
		flex: 1 1 33.3%;
		width: 33.3%;
		padding: 0 1rem;

		@include r(320)  { flex: 1 1 100%; width: 100%; }
		@include r(480)  { flex: 1 1 100%; width: 100%; }
		@include r(768)  { flex: 1 1 100%; width: 100%; }
		@include r(1024) { flex: 1 1 33.3%; width: 33.3%; }
		@include r(1140) { flex: 1 1 33.3%; width: 33.3%; }
		@include r(1280) { flex: 1 1 33.3%; width: 33.3%; }
	}

	.span2 {
		flex: 1 1 16.67%;
		width: 16.67%;
		padding: 0 1rem;

		@include r(320)  { flex: 1 1 100%; width: 100%; }
		@include r(480)  { flex: 1 1 100%; width: 100%; }
		@include r(768)  { flex: 1 1 100%; width: 100%; }
		@include r(1024) { flex: 1 1 16.67%; width: 16.67%; }
		@include r(1140) { flex: 1 1 16.67%; width: 16.67%; }
		@include r(1280) { flex: 1 1 16.67%; width: 16.67%; }
	}
}

.disc-list {
	ul {
		padding: 0;
		list-style-type: none;

		li {
			background: url( '../../images/icons/icon-disc-small.png' ) 12px 12px no-repeat;
			padding: 0 0 0 3rem;
		}
	}
}

.small-arrow-list {
	ul {
		padding: 0;
		list-style-type: none;

		li {
			background: url( '../../images/icons/icon-arrow-small.png' ) 12px 9px no-repeat;
			padding: 0 0 0 3rem;
		}
	}
}

.tick-list {
	ul {
		padding: 0;
		list-style-type: none;

		li {
			background: url( '../../images/icons/icon-tick-list.png' ) 8px 7px no-repeat;
			padding: 0 0 0 3rem;
		}
	}
}

.arrow-list {
	ul {
		padding: 0;
		list-style-type: none;

		li {
			background: url( '../../images/icons/icon-arrow.png' ) 9px 10px no-repeat;
			padding: 0 0 0 3rem;
		}
	}
}
