/**
 * Compare Property Section
 */

.rh_compare {
	display: none;
	width: 100%;
	padding: 2rem;
	margin: 0 0 3rem 0;
	text-align: left;
	background: #fff;
	box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

	@include r(320)  { margin: 0 0 3rem 0; }
	@include r(480)  { margin: 0 0 3rem 0; }
	@include r(768)  { margin: 0 0 3rem 0; }
	@include r(1024) { margin: 0 0 3rem 0; }
	@include r(1140) { margin: 0 0 3rem 0; }
	@include r(1280) { margin: 0 0 3rem 0; }

	.title {
		margin: 0;
		margin-bottom: 1.5rem;
		font-size: 1.5rem;
		font-weight: 500;
		line-height: 1.5;
	}

	.rh_compare__carousel {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -1rem;

		.rh_compare__slide {
			position: relative;
			padding: 0 1rem;
			margin-bottom: 1.5rem;
			width: 25%;

			@include r(320)  { width: 100%; }
			@include r(480)  { width: 50%; }
			@include r(768)  { width: 25%; }
			@include r(1024) { width: 25%; }
			@include r(1140) { width: 25%; }
			@include r(1280) { width: 25%; }

			img {
				width: 100%;
			}
		}

		.rh_compare__remove {
			position: absolute;
			top: 0;
			right: 1rem;
			padding: 0.2rem 0.5rem;
			background: rgba(#000, 0.2);
			transition: 0.2s all linear;

			&:hover {
				background: rgba(#000, 0.4);
				transition: 0.2s all linear;
			}

			i {
				color: #fff;
			}
		}
	}
}

.rh_compare--map_variation {
	.rh_compare__carousel {
		.rh_compare__slide {
			width: 33%;

			@include r(320)  { width: 100%; }
			@include r(480)  { width: 50%; }
			@include r(768)  { width: 33%; }
			@include r(1024) { width: 33%; }
			@include r(1140) { width: 33%; }
			@include r(1280) { width: 33%; }
		}
	}
}
