/**
 * Base Styles
 */

/* ----------------------------------------------------------------------------
* Base
* ------------------------------------------------------------------------- */
// Base file for default font sizes.


p,
a {
	color: $rh_text_color;
	font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.2;

    @include r(320)  { font-size: 1.4rem; }
    @include r(480)  { font-size: 1.5rem; }
    @include r(768)  { font-size: 1.5rem; }
    @include r(1024) { font-size: 1.5rem; }
    @include r(1140) { font-size: 1.5rem; }
    @include r(1280) { font-size: 1.5rem; }
}

// Typesetting variables. Edit these!
$baseFontSize:14; // in pixels. This would result in 22px on desktop
$baseLineHeight:2; // how large the line height is as a multiple of font size
$leading:$baseLineHeight * 1rem;
// Rate of growth for headings
// I actually like this to be slightly smaller then the leading. Makes for tight headings.
$scale:1.8;

h1,
h2,
h3,
h4,
h5,
h6 {
    /* Change heading typefaces here */
    font-family: $rubikFontFamily;
    font-weight: 700;
    margin-top: $leading;
    margin-bottom: 0;
    line-height: 1.2;
    color: $rh_heading_color;
}
h1 {
    font-size: 3 * $scale * 1rem;
    margin-top: 2 * $leading;
}
h2 {
    font-size: 2 * $scale * 1rem;
    margin-top: 2 * $leading;
}
h3 {
    font-size: 1 * $scale * 1rem;
}
h4 {
    font-size: $scale / 1.1 * 1rem;
}
h5 {
    font-size: $scale / 1.3 * 1rem;
}
h6 {
    font-size: $scale / 1.5 * 1rem;
}

::selection {
    color: #fff;
    background: $rh_primary_color;
}

span,
dl,
ol,
ul,
table,
pre,
figure,
legend,
input,
textarea,
button,
label,
select {
	font-size: 1.5rem;
    font-weight: 400;
    line-height: 1.2;
}

input,
textarea {
	padding: 0.5rem;
	outline: 0;
	box-shadow: none;
}

button {
	box-shadow: none;
	background: $rh_btn_bg;
	color: #fff;
	border: none;
	padding: 0.5rem 1rem;
	outline: 0;

	&:focus {
		outline: 0;
	}
}

blockquote {
    background-color: rgba($rh_primary_color, 0.1);
    border-color: $rh_primary_color;
    font-size: 1.5rem;
    border-left: 5px solid $rh_primary_color;
}

table {
    width: 100%;
    margin-bottom: 2rem;
    table-layout: fixed;
}

table,
th,
td {
    border: 1px solid rgba($rh_text_color, 0.2);
    border-collapse: collapse;
}

th {
    color: $rh_heading_color;
    font-size: 1.5rem;
    font-weight: 500;
    line-height: 1.5;
    padding: 0.5rem 0.7rem;
}

td {
    font-size: 1.5rem;
    line-height: 1.5;
    padding: 0.5rem 0.7rem;
}

dl {
    margin-bottom: 2rem;

    dt {
        font-size: 1.8rem;
        font-weight: 500;
        line-height: 1.5;
        color: $rh_heading_color;
    }

    dd {
        font-size: 1.5rem;
        line-height: 2;
        margin-bottom: 1.5rem;
    }
}

address {
    margin-bottom: 2rem;
}

pre {
    margin-bottom: 2rem;
    line-height: 1.5;
}

.rh_wrap {
	position: relative;
    overflow: hidden;
    z-index: 0;
    margin: 0 auto;
}

.rh_wrap--padding {
    padding: 0 10rem;
    max-width: 1440px;
    margin: 0 auto;

    @include r(320)  { padding: 0 2rem; }
    @include r(480)  { padding: 0 3rem; }
    @include r(768)  { padding: 0 5rem; }
    @include r(1024) { padding: 0 5rem; }
    @include r(1140) { padding: 0 7rem; }
    @include r(1280) { padding: 0 10rem; }
}

.rh_wrap--topPadding {
    padding-top: 8rem;

    @include r(320)  { padding-top: 2rem; }
    @include r(480)  { padding-top: 3rem; }
    @include r(768)  { padding-top: 5rem; }
    @include r(1024) { padding-top: 5rem; }
    @include r(1140) { padding-top: 7rem; }
    @include r(1280) { padding-top: 8rem; }
}

.clearfix {
	@include clearfix();
}

.rh_svg {
    fill: #1ea69a;
}

.hide {
    display: none;
}

.highlight {
    svg {
        path {
            fill: $rh_secondary_color;
        }
    }
}

.highlight__red {
    svg {
        path {
            fill: #ea3d3d;
        }
    }
}
