/**
 * Section: Partners
 */

.rh_section__partners {
	background: #fff;
	padding: 5rem 9rem 10rem;
	position: relative;

	@include r(320)  { padding: 2rem 0 0; }
	@include r(480)  { padding: 3rem 0 0; }
	@include r(768)  { padding: 5rem 0 0; }
	@include r(1024) { padding: 9rem 0; }
	@include r(1140) { padding: 9rem 0; }
	@include r(1280) { padding: 9rem 0; }

	&:before {
		content: '';
	    color: transparent;
	    width: 0;
	    height: 0;
	    border-top-width: 180px;
	    border-bottom-width: 0px;
	    border-right: 1440px solid #fff;
	    border-right: 100vw solid #fff;
	    position: absolute;
	    top: -90px;
	    left: 0;
	    z-index: 9999;

	    @include r(320)  { border-right-width: 0; border-top-width: 0; }
		@include r(480)  { border-right-width: 0; border-top-width: 0; }
		@include r(768)  { border-right-width: 0; border-top-width: 0; }
		@include r(1024) { top: -70px; border-right-width: 100vw; border-top-width: 140px; }
		@include r(1140) { top: -80px; border-right-width: 100vw; border-top-width: 160px; }
		@include r(1280) { top: -90px; border-right-width: 100vw; border-top-width: 180px; }
	}

	&:after {
		content: '';
		color: transparent;
	    width: 0;
	    height: 0;
        border-bottom-width: 180px;
	    border-top-width: 0px;
	    border-left: 1440px solid #fff;
	    border-left: 100vw solid #fff;
	    position: absolute;
	    bottom: -90px;
	    left: 0;
	    z-index: 99999;

	    @include r(320)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(480)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(768)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(1024) { bottom: -50px; border-left-width: 100vw; border-bottom-width: 140px; }
		@include r(1140) { bottom: -70px; border-left-width: 100vw; border-bottom-width: 160px; }
		@include r(1280) { bottom: -90px; border-left-width: 100vw; border-bottom-width: 180px; }
	}

	.rh_section__head {
		@include r(320)  { padding: 0 2rem 3rem; }
		@include r(480)  { padding: 0 3rem 3rem; }
		@include r(768)  { padding: 0 5rem 5rem; }
		@include r(1024) { padding: 0 5rem 5rem; }
		@include r(1140) { padding: 0 7rem 7rem; }
		@include r(1280) { padding: 0 10rem 7rem; }
	}
}

.rh_section__partners_wrap {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	max-width: 1440px;
	margin: 0 auto;

	@include r(320)  { padding: 0 2rem 2rem; }
	@include r(480)  { padding: 0 3rem 3rem; }
	@include r(768)  { padding: 0 4rem 5rem; }
	@include r(1024) { padding: 0 4rem 5rem; }
	@include r(1140) { padding: 0 6rem 5rem; }
	@include r(1280) { padding: 0 9rem 5rem; }
}

.rh_partner {
	flex: 0 1 auto;
	width: 16.6%;
	text-align: center;
	padding: 0 1rem 2rem;
	z-index: 1;

	@include r(320)  { width: 100%; padding: 0 1rem 2rem; }
	@include r(480)  { width: 80%; padding: 0 1rem 3rem; }
	@include r(768)  { width: 25%; padding: 0 1rem 2rem; }
	@include r(1024) { width: 16.6%; padding: 0 1rem 2rem; }
	@include r(1140) { width: 16.6%; padding: 0 1rem 2rem; }
	@include r(1280) { width: 16.6%; padding: 0 1rem 2rem; }
}
