/**
 * Google Maps
 */

.rh_map {
	display: block;

	#map-head {
		height: 600px;
		height: 60rem;

		#listing-map {
			height: 100%;
		}
	}
}

.rh_map__search {
	#map-head {
		margin-top: -5.3rem;

		@include r(320)  { margin-top: 2rem; }
		@include r(480)  { margin-top: 3rem; }
		@include r(768)  { margin-top: 5rem; }
		@include r(1024) { margin-top: -4.5rem; }
		@include r(1140) { margin-top: -4rem; }
		@include r(1280) { margin-top: -4.75rem; }
	}
}
