/**
 * Widgets
 */

.rh_widgets {
	display: block;
	width: 100%;
	margin: 0 auto;
	color: $rh_text_color;
}

.widget {
	display: block;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	margin-bottom: 3rem;

	p {
		color: $rh_text_color;
		line-height: 2;
		margin-bottom: 2rem;
	}

	.qaet-tweet {
		small {
			opacity: 1;
		}
	}

	* {
		font-size: 1.5rem;
	}

	.rh_widget_search__form,
	.mc-wrapper {
		-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
	}

	.title {
		color: #fff;
		font-weight: 500;
		margin: 0 auto;
		margin-bottom: 2rem;
	}

	a {
		transition: 0.2s color linear;

		&:hover {
			color: #fff;
			transition: 0.2s color linear;
		}
	}

	ul,
	ol {
		display: block;
		width: 100%;
		margin-top: 1rem;
		list-style-type: none;

		.children {
			margin: 0;
			padding: 0 0 0 1rem;
		}

		li {
			color: $rh_text_color;
			margin: 0;
			line-height: 2;
		}

		a {
			display: inline-block;
		}

		.sub-menu {
			margin: 1rem 0 1rem 1rem;
		}
	}

	select {
		color: $rh_text_color;
		border-bottom: 1px solid $rh_text_color;
		border-radius: 0;
		padding: 1.5rem 0;
		outline: 0;

		&:focus {
			outline: 0;
		}
	}

	.calendar_wrap {
		// margin-bottom: 2rem;
	}

	table {
		border: 1px solid $rh_text_color;
		border-collapse: collapse;
		width: 100%;
		margin: 0;

		caption {
			color: $rh_text_color;
			margin-bottom: 1rem;
			text-align: left;
		}

		td,
		th {
			border: 1px solid $rh_text_color;
			color: $rh_text_color;
			font-size: 1.4rem;
			text-align: center;
			padding: 1rem;
		}
	}

	.rssSummary {
		line-height: 1.5;
	}

	.searchform {
		div {
			display: flex;
			flex-direction: column;
		}

		input[type="text"] {
			border-bottom: 1px solid $rh_text_color;
			color: $rh_text_color;
			padding: 1.5rem 0;
			margin: 0;
		}

		input[type="submit"] {
			@extend .rh_btn;
			@extend .rh_btn--primary;
			cursor: pointer;
			margin-top: 1.5rem;
		}
	}
}

.Featured_Properties_Widget {
	// margin: 0;
}

.Agent_Properties_Widget {
	// margin: 0;
}

.Agent_Featured_Properties_Widget {
	// margin: 0;
}

.widget_rss {
	.title {
		display: flex;
		align-items: center;

		a {
			display: flex;
			margin-right: 1rem;
			color: #fff;
			font-weight: 500;
		}
	}
}

.textwidget {
	color: $rh_text_color;

	p {
		color: $rh_text_color;
		line-height: 2;
		margin-bottom: 2rem;
	}

	div.wp-caption {
		img {
			margin: 0;
		}
	}

	p.wp-caption-text {
		font-size: 1.5rem;
		padding: 0 0 2rem 0;
	}

	img {
		margin: 1rem 0;
	}
}

.tagcloud {
	a {
		display: inline-block;
	    margin: 0 0 0.5rem;
	    padding: 0.7rem 1rem;
	    font-size: 1.5rem !important;
	}
}

.widget {
	.tagcloud {
		a {
			color: #fff;
	    	background: $rh_primary_color;
	    	transition: 0.2s all linear;

	    	&:hover {
	    		color: #fff;
	    		background: $rh_secondary_color;
	    		transition: 0.2s all linear;
	    	}
		}
	}
}

.widget.widget_mc4wp_form_widget {
	input[type="text"],
	input[type="email"] {
		display: block;
		width: 100%;
		background: #fff;
		padding: 1.5rem 2rem;
		margin-bottom: 1.5rem;
	}
	input[type="submit"] {
		@extend .rh_btn;
		@extend .rh_btn--secondary;
		cursor: pointer;
		font-weight: 500;
		margin-bottom: 2rem;
	}

	.mc4wp-alert {
		p {
			color: $rh_secondary_color;
			line-height: 1.5;
		}
	}
}

.rh_contact_widget {
	display: block;

	@include r(320)  { margin-bottom: 2rem; }
	@include r(480)  { margin-bottom: 2rem; }
	@include r(768)  { margin-bottom: 0; }
	@include r(1024) { margin-bottom: 0; }
	@include r(1140) { margin-bottom: 0; }
	@include r(1280) { margin-bottom: 0; }

	.rh_contact_widget__item {
		display: flex;
		justify-content: flex-start;
		padding: 0 0 2rem 0;
		line-height: 1.5;

		.icon {
			padding: 0 1.5rem;

			@include r(320)  { padding: 0 1.5rem 0 0; }
			@include r(480)  { padding: 0 1.5rem 0 0; }
			@include r(768)  { padding: 0 1.5rem; }
			@include r(1024) { padding: 0 1.5rem; }
			@include r(1140) { padding: 0 1.5rem; }
			@include r(1280) { padding: 0 1.5rem; }

			svg {
				fill: $rh_text_color;
			}
		}

		.content {
			font-size: 1.5rem;
			line-height: 2;
			max-width: 25rem;
		}

		a.content {
			display: inline-block;
			transition: 0.2s all linear;

			&:hover {
				color: #fff;
				transition: 0.2s all linear;
			}
		}
	}

	.rh_contact_widget--alignTop {
		align-items: flex-start;
	}

	.rh_contact_widget--alignCenter {
		align-items: center;
	}

	.rh_contact_widget--alignBaseline {
		align-items: baseline;
	}
}

// Sidebar Widgets
.rh_sidebar {
	.widget {
		.title {
			color: $rh_heading_color;
			font-size: 2.2rem;
			font-weight: 400;
			margin: 0 0 2rem 0;
			text-align: left;
		}
	}

	.Featured_Properties_Widget,
	.Agent_Featured_Properties_Widget,
	.Agent_Properties_Widget {
		.title {
			margin: 0 0 3rem 0;
		}
	}

	.widget_rss {
		.title {
			a {
				color: $rh_heading_color;
			}
		}
	}
}

.rh_widgets {
	.widget_nav_menu {
		.menu {
			display: flex;
			flex-wrap: wrap;
			margin: 0;
		}

		.menu-item {
			width: 50%;
			line-height: 1;
			margin-bottom: 2rem;

			@include r(320)  { width: 100%; }
			@include r(480)  { width: 50%; }
			@include r(768)  { width: 50%; }
			@include r(1024) { width: 50%; }
			@include r(1140) { width: 50%; }
			@include r(1280) { width: 50%; }
		}

		.sub-menu {
			display: none;
		}
	}
}
