/**
 * Header Styles
 */

.rh_header {
	position: absolute;
	top: 0;
	width: 100%;
	box-sizing: border-box;
	z-index: 999;
	background: $rh_banner_bg;
	background: -moz-linear-gradient(270deg, rgba(0,0,0,0.7) 0%, rgba(192,192,192,0) 100%); /* ff3.6+ */
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0,0,0,0.7)), color-stop(100%, rgba(192,192,192,0))); /* safari4+,chrome */
	background: -webkit-linear-gradient(270deg, rgba(0,0,0,0.7) 0%, rgba(192,192,192,0) 100%); /* safari5.1+,chrome10+ */
	background: -o-linear-gradient(270deg, rgba(0,0,0,0.7) 0%, rgba(192,192,192,0) 100%); /* opera 11.10+ */
	background: -ms-linear-gradient(270deg, rgba(0,0,0,0.7) 0%, rgba(192,192,192,0) 100%); /* ie10+ */
	background: linear-gradient(180deg, rgba(0,0,0,0.7) 0%, rgba(192,192,192,0) 100%); /* w3c */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#c0c0c0',GradientType=0 ); /* ie6-9 */

	.rh_header__wrap {
		max-width: 1440px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 auto;
		padding: 2.5rem 10rem;

		@include r(320)  { padding: 2rem; }
		@include r(480)  { padding: 2rem 3rem; }
		@include r(768)  { padding: 2.5rem 5rem; }
		@include r(1024) { padding: 2.5rem 5rem; }
		@include r(1140) { padding: 2.5rem 7rem; }
		@include r(1280) { padding: 2.5rem 10rem; }
	}
}

.rh_logo {
	flex: 0 1 auto;

	.rh_logo__heading {
		display: flex;
		margin: 0;
	    color: #fff;
	    line-height: 1;

	    a {
	    	color: #fff;
		    font-weight: 500;
		    font-size: 2rem;
		    transition: 0.2s all linear;
	    }
	}
}

.rh_menu {
	display: flex;
	flex: 0 1 auto;
	flex-wrap: wrap;
	align-items: center;
	justify-content: flex-end;
	padding: 0;

	.main-menu {
		order: 1;
		position: relative;

		@include r(320)  { order: 2; }
		@include r(480)  { order: 2; }
		@include r(768)  { order: 2; }
		@include r(1024) { order: 2; }
		@include r(1140) { order: 1; }
		@include r(1280) { order: 1; }
	}

	.rh_menu__hamburger {
		display: none;
		padding: 0;
		height: 4.3rem;

		@include r(320)  { display: flex; align-items: center; padding: 1.2rem 0 1.2rem 1.5rem; }
		@include r(480)  { display: flex; align-items: center; padding: 1.2rem 0 1.2rem 1.5rem; }
		@include r(768)  { display: flex; align-items: center; padding: 1.2rem 0 1.2rem 1.5rem; }
		@include r(1024) { display: flex; align-items: center; padding: 1.2rem 0 1.2rem 1.5rem; }
		@include r(1140) { display: none; padding: 0; }
		@include r(1280) { display: none; padding: 0; }
	}
}

ul.rh_menu__main {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;
	position: relative;
	list-style-type: none;
	margin: 0;
	padding: 0 0 0 1.5rem;

	@include r(320)  { display: none; }
	@include r(480)  { display: none; }
	@include r(768)  { display: none; }
	@include r(1024) { display: none; }
	@include r(1140) { display: flex; }
	@include r(1280) { display: flex; }

	& > li {
		position: relative;

		&:hover {
			& > .sub-menu {
				visibility: visible; /* shows sub-menu */
				opacity: 1;
				z-index: 1;
				transform: translateY(0%);
				transition-delay: 0s, 0s, 0.2s;
			}
		}
	}

	& > .current-menu-item {
		& > a {
			background-color: $rh_menu_hover;
		}
	}

	li {
		display: flex;
		&:hover {
			background-color: $rh_menu_hover;
		}

		a {
			color: #fff;
			line-height: 2.4rem;
			padding: 1.3rem 1.7rem;
			text-decoration: none;
			transition: 0.2s all linear;

			&:hover {
				background: $rh_menu_hover;
				transition: 0.2s all linear;
			}
		}
	}

	ul.sub-menu {
	    display: inline-block;
	    width: 230px;
	    background: #fff;
	    border-top: 4px solid $rh_menu_hover;
		padding: 2rem 0;
		visibility: hidden; /* hides sub-menu */
		opacity: 0;
		position: absolute;
		top: 100%;
		left: 0;
		transform: translateY(-1rem);
		z-index: -1;
		transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;

		li {
			width: 100%;
			padding: 0;
			box-sizing: border-box;
			position: relative;
			transition: 0.2s all linear;

			a {
				display: block;
				width: 100%;
				color: $rh_text_color;
				padding: 1rem 2.5rem;

				&:hover {
					background: transparent;
					color: #000;
				}
			}

			&:hover {
				background: $rh_body_bg;
				cursor: pointer;
				transition: 0.2s all linear;

				.sub-menu {
					visibility: visible; /* shows sub-menu */
					opacity: 1;
					z-index: 1;
					transform: translateX(0%);
					transition-delay: 0s, 0s, 0.2s;
				}
			}

		}

		ul.sub-menu {
			display: inline-block;
		    width: 230px;
		    background: #fff;
		    border-top: 4px solid $rh_menu_hover;
			padding: 2rem 0;
			visibility: hidden; /* hides sub-menu */
			opacity: 0;
			position: absolute;
		    top: 0;
			left: 100%;
			transform: translateX(1rem);
			z-index: -1;
			transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;

			a {
				color: $rh_text_color;

				&:hover {
					background: transparent;
					color: #000;
				}
			}
		}
	}
}

.rh_menu__user {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	order: 2;

	@include r(320)  { order: 1 }
	@include r(480)  { order: 1 }
	@include r(768)  { order: 1 }
	@include r(1024) { order: 1 }
	@include r(1140) { order: 2 }
	@include r(1280) { order: 2 }

	.rh_menu__user_phone {
		display: flex;
		align-items: center;
		padding: 0 0 0 1rem;

		@include r(320)  { display: none; padding: 0 0 0 1rem; }
		@include r(480)  { display: flex; padding: 0 0 0 1rem; }
		@include r(768)  { display: flex; padding: 0 0 0 1rem; }
		@include r(1024) { display: flex; padding: 0 0 0 1rem; }
		@include r(1140) { display: flex; padding: 0 0 0 0.5rem; }
		@include r(1280) { display: flex; padding: 0 0 0 1.5rem; }

		svg {
			fill: #fff;
		}

		.contact-number {
			display: inline-block;
			color: #fff;
			font-weight: 500;
			margin: 0 0 0 1rem;

			@include r(320)  { margin: 0 0 0 1rem; }
			@include r(480)  { margin: 0 0 0 1rem; }
			@include r(768)  { margin: 0 0 0 1rem; }
			@include r(1024) { margin: 0 0 0 1rem; }
			@include r(1140) { margin: 0 0 0 0.5rem; }
			@include r(1280) { margin: 0 0 0 1rem; }
		}
	}

	.rh_menu__user_profile {
		display: flex;
		padding: 0 0 0 1rem;
		position: relative;

		@include r(320)  { padding: 0 0 0 1rem; }
		@include r(480)  { padding: 0 0 0 1rem; }
		@include r(768)  { padding: 0 0 0 1rem; }
		@include r(1024) { padding: 0 0 0 1rem; }
		@include r(1140) { padding: 0 0 0 0.5rem; }
		@include r(1280) { padding: 0 0 0 1rem; }

	    .user-icon {
	    	fill: #fff;
	    	width: 38px;
	    	height: 38px;
	    	border-radius: 500rem;
	    	cursor: pointer;
	    	padding: 0.5rem;
	    	transition: 0.2s all linear;

	    	&:hover {
	    		background: rgba( $gray, 0.3 );
	    		transition: 0.2s all linear;
	    	}
	    }
	}

	.rh_menu__user_submit {
		padding: 0 0 0 1rem;

		a {
			display: inline-block;
			background: $rh_btn_bg;
			color: #fff;
			font-weight: 500;
			padding: 1.4rem 2rem;
			text-decoration: none;
			transition: 0.2s all linear;

			@include r(320)  { display: none; padding: 1.4rem 2rem; }
			@include r(480)  { display: none; padding: 1.4rem 2rem; }
			@include r(768)  { display: inline-block; padding: 1.2rem 1.7rem; }
			@include r(1024) { display: inline-block; padding: 1.4rem 1.5rem; }
			@include r(1140) { display: inline-block; padding: 1.4rem 1.5rem; }
			@include r(1280) { display: inline-block; padding: 1.4rem 2rem; }

			&:hover {
				background: darken( $rh_btn_bg, 2% );
				transition: 0.2s all linear;
			}

		}
	}
}

.rh_menu--hover {
	background: $rh_menu_hover;
	transition: 0.2s all linear;
}


// Login Modal
.rh_modal {
	display: inline-block;
    width: 260px;
	visibility: hidden; /* hides sub-menu */
	opacity: 0;
	position: absolute;
	top: 5.5rem;
    right: 2rem;
	transform: translateY(2em);
	z-index: -1;
	transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;

	@include r(320)  { width: 225px; }
	@include r(480)  { width: 260px; }
	@include r(768)  { width: 260px; }
	@include r(1024) { width: 260px; }
	@include r(1140) { width: 260px; }
	@include r(1280) { width: 260px; }

	.rh_modal__corner {
		display: block;
		width: 10px;
    	height: 10px;
		margin: 0 0 0 auto;
		border-top: 10px solid transparent;
	    border-right: 20px solid $rh_menu_bg;
	    position: relative;
	}

	.rh_modal__wrap {
		display: block;
		text-align: center;
		background: $rh_menu_bg;
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

		label {
			display: block;
			text-align: left;
			margin: 0.5rem 0;
			font-size: 1.4rem;
			padding: 0;
			color: #444;
		}

		.modal-loader {
			display: none;
			width: 35px;
			height: 35px;
		}

		.rh_modal__msg {
			margin-bottom: 1rem;
			font-size: 1.4rem;
			line-height: 1.5;
		}

		.rh_modal__login_wrap {
			padding: 2.5rem;
		}

		h3 {
			font-weight: 500;
			margin: 0 0 1rem 0;
			text-align: left;
		}

		input {
			background: #fff;
			font-size: 1.5rem;
			padding: 1.5rem;
			width: 100%;
			border-bottom: 2px solid $rh_menu_bg;
		}

		button {
			width: 100%;
			padding: 1.5rem;
			cursor: pointer;
			margin: 0;
			margin-bottom: 1.5rem;
			transition: 0.2s background linear;

			&:hover {
				background: darken( $rh_btn_bg, 2% );
				transition: 0.2s background linear;
			}
		}

		a {
			display: inline-block;
			font-size: 1.5rem;
			font-weight: 500;
			margin-bottom: 1rem;

			&:hover {
				color: $rh_menu_hover;
				text-decoration: underline;
			}
		}

		.rh_modal__dashboard {
			display: block;
			padding: 2.5rem 2.5rem 1rem;
			background: #fff;
			text-align: left;

			.rh_modal__dash_link {
				display: flex;
				align-items: center;
				width: 100%;
				margin-bottom: 2.5rem;
				transition: 0.2s all linear;

				&:hover {
					svg {
						fill: $rh_secondary_color;
						transition: 0.2s all linear;
					}
				}

				svg {
					width: 16px;
					fill: $rh_text_color;
				}

				span {
					padding: 0 0 0 1.5rem;
				}
			}
		}
	}

	.rh_modal__recaptcha {
		margin-top: 2px;

		.inspiry-recaptcha-wrapper {
			height: 56px;
		}
	}
}

.rh_modal--display {
	visibility: visible; /* shows sub-menu */
	opacity: 1;
	z-index: 1;
	transform: translateY(0%);
	transition-delay: 0s, 0s, 0.2s;
}

.rh_modal__user_icon {
	background: rgba( $gray, 0.3 );
	transition: 0.2s all linear;
}

.rh_modal__msg {
	margin-bottom: 1rem;
}

.rh_modal__register_wrap,
.rh_modal__forgot_wrap {
	display: none;
	padding: 2.5rem;
}

// Modal User
.rh_user {
	display: flex;
	margin: 0 auto;
	padding: 2.5rem;

	.rh_user__avatar {
		padding: 0 1.5rem 0 0;

		img {
			width: 4rem;
			height: 4rem;
			border-radius: 500rem;
			padding: 0;
		}
	}

	.rh_user__details {
		text-align: left;

		.rh_user__msg {
			font-size: 1.3rem;
			margin-bottom: 0.2rem;
		}

		.rh_user__name {
			font-size: 1.5rem;
			margin-bottom: 0;
			font-weight: 500;
		}
	}
}


// Responsive Menu
.rh_menu__responsive {
	display: block;
	width: 25rem;
	margin-top: 1.5rem;
	padding: 1.5rem 0;
	background: #fff;
	position: absolute;
	top: 0;
	right: 0;
	transform: translateY(0);
	visibility: hidden;
	opacity: 0;
	z-index: -1;
	transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;

	li {
		padding: 0;
		position: relative;

		a {
			display: block;
			padding: 1.5rem 2.5rem;
			line-height: 1.5;
			transition: 0.2s all linear;

			&:hover {
				transition: 0.2s all linear;
			}
		}
	}

	.rh_menu__indicator {
		display: flex;
	    align-items: center;
	    justify-content: center;
	    width: 4rem;
	    height: 5rem;
	    position: absolute;
	    right: 0;
	    top: 0;
	    line-height: 0.4;
	    cursor: pointer;
	    color: $rh_text_color;
	    transition: 0.2s all linear;
	}

	.rh_menu__indicator_up {
		transform: rotate(180deg);
		transition: 0.2s all linear;
	}

	ul.sub-menu {
		display: none;
		width: 25rem;
		padding: 1.5rem 0;
		margin: 0;
		background: darken(#fff, 5%);

		ul.sub-menu {
			display: none;
			width: 25rem;
			padding: 1.5rem 0;
			margin: 0;
			background: darken(#fff, 10%);
		}

		ul.rh_menu__submenu_third {
			left: 2.5rem;
			visibility: visible;
			z-index: 1;
			opacity: 1;
			transition-delay: 0s, 0s, 0.2s;
		}
	}

	ul.rh_menu__submenu_second {
		left: 2.5rem;
		visibility: visible;
		z-index: 1;
		opacity: 1;
		transition-delay: 0s, 0s, 0.2s;
	}
}

.rh_menu__responsive_show {
	transform: translateY(4.3rem);
	visibility: visible;
	opacity: 1;
	z-index: 1;
	transition-delay: 0s, 0s, 0.2s;
}


// Progress Bar
#rh_progress {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 5px;
	z-index: 99999;
}
