/**
 * Advance Property Search
 */

.rh_prop_search {
	max-width: 1440px;
	margin: 0 auto;
}

.rh_prop_search--page {
	padding-bottom: 5rem;

	@include r(320)  { padding-bottom: 2rem; }
	@include r(480)  { padding-bottom: 3rem; }
	@include r(768)  { padding-bottom: 5rem; }
	@include r(1024) { padding-bottom: 5rem; }
	@include r(1140) { padding-bottom: 5rem; }
	@include r(1280) { padding-bottom: 5rem; }
}

.rh_prop_search__form {
	display: flex;
	position: relative;
    margin-top: -4.25rem;
    z-index: 99;

    @include r(320)  { flex-wrap: wrap; margin-top: 0; }
	@include r(480)  { flex-wrap: wrap; margin-top: 0; }
	@include r(768)  { flex-wrap: wrap; margin-top: 0; }
	@include r(1024) { flex-wrap: nowrap; margin-top: -4.5rem; }
	@include r(1140) { flex-wrap: nowrap; margin-top: -4rem; }
	@include r(1280) { flex-wrap: nowrap; margin-top: -4.75rem; }

	.rh_prop_search__fields {
		display: flex;
		flex: 0 1 auto;
		flex-direction: column;
		justify-content: flex-start;
		background: #fff;
		width: 80%;
		position: relative;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 100%; }
		@include r(1024) { width: 77%; }
		@include r(1140) { width: 80%; }
		@include r(1280) { width: 80%; }

		.rh_prop_search__wrap {
			display: flex;
			flex-direction: row;
			border-bottom: 1px solid rgba( $textColor, 0.2 );
			border-left: 1px solid rgba( $textColor, 0.2 );

			@include r(320)  { flex-wrap: wrap; border-bottom: none; }
			@include r(480)  { flex-wrap: wrap; border-bottom: none; }
			@include r(768)  { flex-wrap: wrap; border-bottom: 1px solid rgba( $textColor, 0.2 ); }
			@include r(1024) { flex-wrap: nowrap; border-bottom: 1px solid rgba( $textColor, 0.2 ); }
			@include r(1140) { flex-wrap: nowrap; border-bottom: 1px solid rgba( $textColor, 0.2 ); }
			@include r(1280) { flex-wrap: nowrap; border-bottom: 1px solid rgba( $textColor, 0.2 ); }

			.select2-container--default .select2-selection--single {
				padding: 0 2.5rem;

				@include r(320)  { padding: 0 1.5rem; }
				@include r(480)  { padding: 0 1.5rem; }
				@include r(768)  { padding: 0 1.5rem; }
				@include r(1024) { padding: 0 2rem; }
				@include r(1140) { padding: 0 2rem; }
				@include r(1280) { padding: 0 2.5rem; }
			}

			.select2-container--disabled .select2-selection--single {
				background: transparent;
			}
		}

		.rh_prop_search__dropdown {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			width: 100%;
		    opacity: 0;
			visibility: hidden;
			transition: all 0.4s ease-in-out 0s;
			transform: translateY(0);
			position: absolute;
		    top: 0;
		    z-index: -999;
		    border-left: 1px solid rgba( $textColor, 0.2 );

		    @include r(320)  {
		    	opacity: 1;
				visibility: visible;
				transform: translateY(0);
				position: relative;
			    top: 0;
			    z-index: 1;
		    }
			@include r(1024) {
				opacity: 0;
				visibility: hidden;
				transform: translateY(0);
				position: absolute;
			    top: 0;
			    z-index: -999;
			}

			.rh_prop_search__option {
				border-bottom: 1px solid rgba( $textColor, 0.2 );

				&:nth-last-child(1) {
					border-right: 1px solid rgba( $textColor, 0.2 );
				}
			}

			.select2-container--default .select2-selection--single {
				padding: 0 2.5rem;

				@include r(320)  { padding: 0 1.5rem; }
				@include r(480)  { padding: 0 1.5rem; }
				@include r(768)  { padding: 0 1.5rem; }
				@include r(1024) { padding: 0 2rem; }
				@include r(1140) { padding: 0 2rem; }
				@include r(1280) { padding: 0 2.5rem; }
			}

			.select2-container--default.select2-container--disabled .select2-selection--single {
				background: #fff;
			}
		}

		.rh_prop_search__ddActive {
			opacity: 1;
			visibility: visible;
			transition: all 0.4s ease-in-out 0s;
			transform: translateY(9.5rem);

			@include r(320)  { transform: translateY(9.5rem); }
			@include r(480)  { transform: translateY(9.5rem); }
			@include r(768)  { transform: translateY(7.8rem); }
			@include r(1024) { opacity: 1; visibility: visible; transform: translateY(9rem); }
			@include r(1140) { transform: translateY(8rem); }
			@include r(1280) { transform: translateY(9.5rem); }
		}

		.rh_prop_search__option {
			flex: 1 1 auto;
			width: 25%;
			padding: 2rem 0;
			background: #fff;
			border-right: 1px solid rgba( $textColor, 0.2 );
			transition: 0.2s all linear;

			&:nth-last-child(1) {
				border-right: none;

				@include r(320)  { border-right: 1px solid rgba( $textColor, 0.2 ); }
				@include r(480)  { border-right: 1px solid rgba( $textColor, 0.2 ); }
				@include r(768)  { border-right: 1px solid rgba( $textColor, 0.2 ); }
				@include r(1024) { border-right: none; }
				@include r(1140) { border-right: none; }
				@include r(1280) { border-right: none; }
			}

			@include r(320)  { width: 100%; padding: 1.5rem 0; border-bottom: 1px solid rgba( $textColor, 0.2 ); }
			@include r(480)  { width: 50%; padding: 1.5rem 0; border-bottom: 1px solid rgba( $textColor, 0.2 ); }
			@include r(768)  { width: 25%; padding: 1.5rem 0; border-bottom: none; }
			@include r(1024) { width: 25%; padding: 2rem 0; border-bottom: none; }
			@include r(1140) { width: 25%; padding: 1.5rem 0; border-bottom: none; }
			@include r(1280) { width: 25%; padding: 2rem 0; border-bottom: none; }

			label {
				display: block;
				font-size: 1.4rem;
				font-weight: 500;
				margin-bottom: 1rem;
				padding: 0 2.5rem;
				cursor: pointer;

				@include r(320)  { padding: 0 1.5rem; margin-bottom: 0.3rem; }
				@include r(480)  { padding: 0 1.5rem; margin-bottom: 0.3rem; }
				@include r(768)  { padding: 0 1.5rem; margin-bottom: 0.3rem; }
				@include r(1024) { padding: 0 2rem; margin-bottom: 0.5rem; }
				@include r(1140) { padding: 0 2rem; margin-bottom: 0.5rem; }
				@include r(1280) { padding: 0 2.5rem; margin-bottom: 1rem; }

				span {
					display: inline-block;
					font-size: 1.4rem;
				}

				.label {
					font-weight: 500;
				}
			}

			span {
				display: block;
				outline: 0;
				border: none;
				padding-left: 0;
				border-radius: 0;
				transition: 0.2s all linear;

				select {
					outline: 0;
					padding: 0 2.5rem !important;
				}
			}

			input {
				display: block;
				padding: 0 2.5rem;
				width: 100%;

				@include r(320)  { padding: 0 1.5rem; }
				@include r(480)  { padding: 0 1.5rem; }
				@include r(768)  { padding: 0 1.5rem; }
				@include r(1024) { padding: 0 2rem; }
				@include r(1140) { padding: 0 2rem; }
				@include r(1280) { padding: 0 2.5rem; }
			}
		}

		.rh_prop_search__select {
			padding: 0;

			@include r(320)  { padding: 0; }
			@include r(480)  { padding: 0; }
			@include r(768)  { padding: 0; }
			@include r(1024) { padding: 0; }
			@include r(1140) { padding: 0; }
			@include r(1280) { padding: 0; }

			&:hover {
				cursor: pointer;
			}

			label {
				@include r(320)  { padding: 1.5rem 1.5rem 0.3rem; margin-bottom: 0; }
				@include r(480)  { padding: 1.5rem 1.5rem 0.3rem; margin-bottom: 0; }
				@include r(768)  { padding: 1.5rem 1.5rem 0.3rem; margin-bottom: 0; }
				@include r(1024) { padding: 2rem 2rem 0.5rem; margin-bottom: 0; }
				@include r(1140) { padding: 1.5rem 2rem 0.5rem; margin-bottom: 0; }
				@include r(1280) { padding: 2rem 2.5rem 1rem; margin-bottom: 0; }
			}

			.rh_prop_search__selectwrap {
				@include r(320)  { padding: 0 0 1.5rem 0; }
				@include r(480)  { padding: 0 0 1.5rem 0; }
				@include r(768)  { padding: 0 0 1.5rem 0; }
				@include r(1024) { padding: 0 0 2rem 0; }
				@include r(1140) { padding: 0 0 1.5rem 0; }
				@include r(1280) { padding: 0 0 2rem 0; }
			}

			.select2-selection__arrow {
				display: inline-block;
				right: 1.5rem;
			}
		}

		.rh_prop_search__active {
			background: $rh_primary_color;
			border-right: none;
			transition: 0.2s all linear;

			label {
				color: #fff;
				transition: 0.2s all linear;
			}
		}
	}

	.rh_prop_search__btnWrap {
		display: flex;
		flex-wrap: nowrap;
		width: 100%;
		background: #18998e;
	}

	.rh_prop_search__buttons {
		display: flex;
		flex: 0 1 auto;
		justify-content: center;
		align-items: flex-start;
		width: 20%;
		position: relative;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 100%; }
		@include r(1024) { width: 23%; }
		@include r(1140) { width: 20%; }
		@include r(1280) { width: 20%; }

		.rh_prop_search__advance {
			display: inline-block;
			width: 38%;
			text-align: center;
			background: #18998e;

			@include r(320)  { display: none; }
			@include r(480)  { display: none; }
			@include r(768)  { display: none; }
			@include r(1024) { display: inline-block; }
			@include r(1140) { display: inline-block; }
			@include r(1280) { display: inline-block; }

			a {
			    display: flex;
			    justify-content: center;
			    width: 100%;
			    height: 100%;
				padding: 3.6rem 0;
				transition: 0.2s background linear;

				@include r(320)  { padding: 2.2rem 0; }
				@include r(480)  { padding: 2.5rem 0; }
				@include r(768)  { padding: 2.7rem 0; }
				@include r(1024) { padding: 3.3rem 0; }
				@include r(1140) { padding: 2.8rem 0; }
				@include r(1280) { padding: 3.6rem 0; }

				&:hover {
			    	background: darken( #18998e, 2% );
			    	transition: 0.2s background linear;
			    }

			    svg {
			    	display: inline-block;
			    	width: 20px;
			    	height: 23px;
			    }
			}
		}

		.rh_prop_search__searchBtn {
			width: 62%;
			text-align: center;

			@include r(320)  { width: 100%; }
			@include r(480)  { width: 100%; }
			@include r(768)  { width: 100%; }
			@include r(1024) { width: 62%; }
			@include r(1140) { width: 62%; }
			@include r(1280) { width: 62%; }

			.rh_btn__prop_search {
				// display: flex;
				// justify-content: center;
				// align-items: center;
				display: block;
				width: 100%;
			    margin: 0;
			    padding: 3.7rem 0;
			    cursor: pointer;
			    transition: 0.2s background linear;

			    @include r(320)  { padding: 2.5rem 0; }
				@include r(480)  { padding: 2.7rem 0; }
				@include r(768)  { padding: 2.9rem 0; }
				@include r(1024) { padding: 3.5rem 0; }
				@include r(1140) { padding: 3rem 0; }
				@include r(1280) { padding: 3.8rem 0; }

			    &:hover {
			    	background: darken( $rh_btn_bg, 2% );
			    	transition: 0.2s background linear;
			    }

			    svg {
			    	display: inline-block;
				    width: 18px;
				    height: 18px;
				    margin-right: 0.5rem;
			    }
			}
		}

		svg {
		}
	}
}

.home .rh_prop_search__buttons {
	&:after {
		content: '';
	    background-image: url(../../images/advance-search-arrow.png);
	    background-size: contain;
	    background-repeat: no-repeat;
	    position: absolute;
	    bottom: -4rem;
	    left: -10.5rem;
	    z-index: -9999;
	    width: 16rem;
	    height: 4rem;

	    @include r(320)  { display: none; }
		@include r(480)  { display: none; }
		@include r(768)  { display: none; }
		@include r(1024) { display: inline-block; left: -11rem; }
		@include r(1140) { display: inline-block; left: -11.3rem; }
		@include r(1280) { display: inline-block; left: -10.5rem; }
	}
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
	border-color: #fff transparent transparent transparent;
}

.rh_prop_search__active {

	.select2-container--default .select2-selection--single {
		background: $rh_primary_color;
		color: #fff;
		transition: 0.2s all linear;
	}

	.select2-container--default .select2-selection--single .select2-selection__rendered {
		color: #fff;
		transition: 0.2s all linear;
	}

	.select2-container--default .select2-selection--single .select2-selection__arrow b {
		border-color: #fff transparent transparent transparent;
	}

	.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
		border-color: transparent transparent #fff transparent;
	}

	.select2-container--default .select2-selection--single {
		.select2-selection__arrow b {
			border-color: #fff transparent transparent transparent;
		}
	}
}

.select2-container--open .select2-dropdown--below,
.select2-container--open .select2-dropdown--above {
	background: $rh_primary_color;
	border-radius: 0;
    border: none;

    .select2-results__option {
    	padding: 1.5rem 2.5rem;
    	color: #fff;
    }
}

.select2-container--default .select2-results__option--highlighted[aria-selected],
.select2-container--default .select2-results__option[aria-selected=true] {
	background-color: darken($rh_primary_color, 5%);
	color: #fff;
	transition: 0.2s all linear;
}

.select2-search--dropdown .select2-search__field {
	padding: 1rem 2.5rem;
}

.rh_prop_search--map_listing {
	padding-bottom: 0;
}

.select2-container--default .select2-search--dropdown .select2-search__field {
	border: 1px solid #fff;
	color: #fff;
}
