/**
 * Slider
 */

.rh_slider {
	margin: 0;

	.flexslider {
		margin: 0;
		border-radius: 0;
		border: none;

		&:hover {
			.flex-direction-nav {
				.flex-prev {
					@include r(320)  { left: 25px; }
					@include r(480)  { left: 25px; }
					@include r(768)  { left: 15px; }
					@include r(1024) { left: 5px; }
					@include r(1140) { left: 25px; }
					@include r(1280) { left: 25px; }
				}
				.flex-next {
					@include r(320)  { right: 25px; }
					@include r(480)  { right: 25px; }
					@include r(768)  { right: 15px; }
					@include r(1024) { right: 5px; }
					@include r(1140) { right: 25px; }
					@include r(1280) { right: 25px; }
				}
			}
		}

		.flex-direction-nav {
			.flex-prev {
				@include r(320)  { display: none; left: 25px; }
				@include r(480)  { display: none; left: 25px; }
				@include r(768)  { display: block; left: 15px; }
				@include r(1024) { display: block; left: -50px; }
				@include r(1140) { display: block; left: -50px; }
				@include r(1280) { display: block; left: -50px; }
			}
			.flex-next {
				@include r(320)  { display: none; right: 25px; }
				@include r(480)  { display: none; right: 25px; }
				@include r(768)  { display: block; right: 15px; }
				@include r(1024) { display: block; right: -50px; }
				@include r(1140) { display: block; right: -50px; }
				@include r(1280) { display: block; right: -50px; }
			}
		}

		ul.slides {
			.slide {
				display: block;
				height: 100%;
				height: 90rem;
				height: 90vh;

				@include r(320)  { height: 40rem; }
				@include r(480)  { height: 40rem; }
				@include r(768)  { height: 50rem; }
				@include r(1024) { height: 90vh; }
				@include r(1140) { height: 90vh; }
				@include r(1280) { height: 90vh; }
			}

			div.rh_slide__desc {
				position: relative;
				max-width: 1440px;
				margin: 0 auto;
			}

			a.read-more {
				display: inline-block;
				height: initial;
				margin-top: 1.5rem;
			}

			li {
				position: relative;
			}
		}
	}
}


.rh_slide__desc {
	width: 100%;

	.rh_slide--max_width {
		position: relative;
		max-width: 1440px;
		margin: auto;
		height: 100%;
	}

	.rh_slide__desc_wrap {
		display: block;
		width: 100%;
		position: absolute;
		background: #fff;
		padding: 5rem;
		max-width: 50rem;

		@include r(320)  { display: none; padding: 3rem; bottom: 10rem; right: 10rem; }
		@include r(480)  { display: none; padding: 3rem; bottom: 10rem; right: 10rem; }
		@include r(768)  { display: block; padding: 3rem; bottom: 5rem; right: 5rem; }
		@include r(1024) { display: block; padding: 5rem; bottom: 7rem; right: 5rem; }
		@include r(1140) { display: block; padding: 5rem; bottom: 7rem; right: 7rem; }
		@include r(1280) { display: block; padding: 5rem; bottom: 10rem; right: 10rem; }
		@include r(1500) { bottom: 20rem; }
	}

	h3 {
		font-size: 2.4rem;
		font-weight: 500;
		margin-top: 0;
		margin-bottom: 1rem;

		.title {
			display: inline-block;
			font-size: 2.4rem;
			font-weight: 500;
			color: $rh_heading_color;
			transition: 0.2s all linear;

			&:hover {
				color: $rh_primary_color;
				transition: 0.2s all linear;
			}
		}
	}

	p {
		line-height: 2;
	}

	.rh_slide__meta_wrap {
		display: flex;
		align-items: flex-start;
		margin: 3rem 0;

		@include r(320)  { display: none; }
		@include r(480)  { display: none; }
		@include r(768)  { display: none; }
		@include r(1024) { display: flex; }
		@include r(1140) { display: flex; }
		@include r(1280) { display: flex; }

		.rh_slide__prop_meta {
			padding: 0 2rem 0 0;

			h4 {
				font-size: 1.3rem;
				font-weight: 500;
				margin-top: 0;
				margin-bottom: 1rem;
			}

			span {
				font-size: 1.4rem;
			}

			span.figure {
				font-weight: 500;
				padding: 0 0 0 1rem;
			}
		}
	}

	.rh_slide_prop_price {
		display: block;

		@include r(320)  { display: none; }
		@include r(480)  { display: none; }
		@include r(768)  { display: none; }
		@include r(1024) { display: block; }
		@include r(1140) { display: block; }
		@include r(1280) { display: block; }

		h4 {
			font-size: 1.3rem;
			font-weight: 500;
			margin-top: 0;
			margin-bottom: 0.5rem;
		}

		span {
			font-size: 2.4rem;
			color: $rh_primary_color;
		}
	}
}

ul.flex-direction-nav {

	a {
		width: 45px;
		height: 45px;
	}

	a.flex-prev {
		&:before {
			content: '';
			background: url('../../images/icons/icon-arrow-left.png') 50% 50% no-repeat;
			width: 45px;
		    height: 45px;
		    background-size: cover;
		}
	}
	a.flex-next {
		&:before {
			content: '';
			background: url('../../images/icons/icon-arrow-right.png') 50% 50% no-repeat;
			width: 45px;
		    height: 45px;
		    background-size: cover;
		}
	}
}
