/**
 * Single Agent Card
 */

.rh_agent_profile {
	display: block;
	margin-top: 1.5rem;
	margin-bottom: 3rem;
	padding: 0;
	text-align: left;

	@include r(320)  { margin-bottom: 1rem; }
	@include r(480)  { margin-bottom: 1rem; }
	@include r(768)  { margin-bottom: 3rem; }
	@include r(1024) { margin-bottom: 3rem; }
	@include r(1140) { margin-bottom: 3rem; }
	@include r(1280) { margin-bottom: 3rem; }
}

.rh_agent_profile__wrap {
	display: block;
	padding: 3rem;
	background: #fff;
	-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
	-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
	box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

	@include r(320)  { padding: 2rem; }
	@include r(480)  { padding: 3rem; }
	@include r(768)  { padding: 3rem; }
	@include r(1024) { padding: 3rem; }
	@include r(1140) { padding: 3rem; }
	@include r(1280) { padding: 3rem; }

	.rh_agent_profile__head {
		display: flex;
		justify-content: space-between;
		padding: 0;
		margin-bottom: 5rem;

		@include r(320)  { flex-direction: column; align-items: center; margin-bottom: 2rem; }
		@include r(480)  { flex-direction: column; align-items: center; margin-bottom: 2rem; }
		@include r(768)  { flex-direction: row; align-items: flex-start; margin-bottom: 2rem; }
		@include r(1024) { flex-direction: row; align-items: flex-start; margin-bottom: 2rem; }
		@include r(1140) { flex-direction: row; align-items: flex-start; margin-bottom: 5rem; }
		@include r(1280) { flex-direction: row; align-items: flex-start; margin-bottom: 5rem; }

		.rh_agent_profile__dp {
			width: 20%;
			margin-top: -4.5rem;
			text-align: center;

			@include r(320)  { width: 60%; text-align: center; }
			@include r(480)  { width: 50%; text-align: center; }
			@include r(768)  { width: 20%; text-align: left; }
			@include r(1024) { width: 20%; text-align: left; }
			@include r(1140) { width: 20%; text-align: left; }
			@include r(1280) { width: 20%; text-align: left; }

			.picture {
				margin: 0;
			}

			.listed_properties {
				margin-top: 1.5rem;

				.number {
					font-size: 2.4rem;
					color: $rh_primary_color;
					margin-bottom: 0.2rem;
				}

				.heading {
					color: $rh_heading_color;
					font-size: 1.3rem;
				}
			}
		}

		.rh_agent_profile__details {
			width: 80%;
			padding: 0 0 0 3rem;

			@include r(320)  { width: 100%; padding: 2rem 0 0; text-align: center; }
			@include r(480)  { width: 100%; padding: 1.5rem 0 0; text-align: center; }
			@include r(768)  { width: 80%; padding: 0 0 0 2rem; text-align: left; }
			@include r(1024) { width: 80%; padding: 0 0 0 3rem; text-align: left; }
			@include r(1140) { width: 80%; padding: 0 0 0 3rem; text-align: left; }
			@include r(1280) { width: 80%; padding: 0 0 0 3rem; text-align: left; }

			.rh_agent_profile__name {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 2.5rem;

				@include r(320)  { flex-direction: column; margin-bottom: 1rem; }
				@include r(480)  { flex-direction: column; margin-bottom: 1rem; }
				@include r(768)  { flex-direction: row; margin-bottom: 1.5rem; }
				@include r(1024) { flex-direction: row; margin-bottom: 2.5rem; }
				@include r(1140) { flex-direction: row; margin-bottom: 2.5rem; }
				@include r(1280) { flex-direction: row; margin-bottom: 2.5rem; }
			}

			.name {
				width: 65%;
				font-size: 2.4rem;
				font-weight: 500;
				margin: 0;
				line-height: 1.5;

				@include r(320)  { width: 100%; margin-bottom: 0.5rem; }
				@include r(480)  { width: 100%; margin-bottom: 0.5rem; }
				@include r(768)  { width: 65%; margin-bottom: 0; }
				@include r(1024) { width: 65%; margin-bottom: 0; }
				@include r(1140) { width: 65%; margin-bottom: 0; }
				@include r(1280) { width: 65%; margin-bottom: 0; }
			}

			.rh_agent_profile__contact {
				display: flex;
				flex-wrap: wrap;

				@include r(320)  { justify-content: center; }
				@include r(480)  { justify-content: center; }
				@include r(768)  { justify-content: flex-start; }
				@include r(1024) { justify-content: flex-start; }
				@include r(1140) { justify-content: flex-start; }
				@include r(1280) { justify-content: flex-start; }
			}

			.detail {
				line-height: 1.5;
				padding: 0 1.5rem 0 0;
				margin: 0 1.5rem 1rem 0;

				@include r(320)  { padding: 0; margin: 0 0.5rem 1rem; }
				@include r(480)  { padding: 0; margin: 0 0.5rem 1rem; }
				@include r(768)  { padding: 0 1.5rem 0 0; margin: 0 1.5rem 1rem 0; }
				@include r(1024) { padding: 0 1.5rem 0 0; margin: 0 1.5rem 1rem 0; }
				@include r(1140) { padding: 0 1.5rem 0 0; margin: 0 1.5rem 1rem 0; }
				@include r(1280) { padding: 0 1.5rem 0 0; margin: 0 1.5rem 1rem 0; }

				span {
					color: $rh_heading_color;
				}

				a {
					color: $rh_heading_color;
					transition: 0.2s all linear;

					&:hover {
						color: $rh_primary_color;
						transition: 0.2s all linear;
					}
				}
			}

			.detail-border {
				display: inline-block;
				background: rgba($rh_text_color, 0.2);
				width: 1px;
				height: 18px;
			}
		}

		.rh_agent_profile__social {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 35%;

			@include r(320)  { width: 80%; }
			@include r(480)  { width: 50%; }
			@include r(768)  { width: 35%; }
			@include r(1024) { width: 35%; }
			@include r(1140) { width: 35%; }
			@include r(1280) { width: 35%; }

			a {
				display: inline-block;
				color: rgba($rh_text_color, 0.5);
				margin: 0.5rem;
				transition: 0.2s all linear;

				&:hover {
					color: $rh_secondary_color;
					transition: 0.2s all linear;
				}

				i {
					font-size: 1.8rem;
				}
			}
		}
	}

	.rh_agent_profile__excerpt {
		border-bottom: 1px solid rgba($rh_text_color, 0.2);
	}
}
