/**
 * Property Agent for Sidebar
 */

.rh_property_agent {
	display: block;
	width: 100%;
	background: #fff;
	text-align: center;
	margin-bottom: 3rem;
	box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

	.agent-image {
		display: inline-block;
		width: 128px;
	}

	img {
		display: inline-block;
    	margin-top: -4rem;
    	padding: 0 0 1rem 0;

    	@include r(320)  { margin-top: 0; padding: 3rem 0 1rem 0; }
		@include r(480)  { margin-top: 0; padding: 3rem 0 1rem 0; }
		@include r(768)  { margin-top: 0; padding: 3rem 0 1rem 0; }
		@include r(1024) { margin-top: -4rem; padding: 0 0 1rem 0; }
		@include r(1140) { margin-top: -4rem; padding: 0 0 1rem 0; }
		@include r(1280) { margin-top: -4rem; padding: 0 0 1rem 0; }
	}

	.rh_property_agent__title {
		margin: 2rem 1rem;
		font-size: 1.9rem;
		font-weight: 500;
	}

	.rh_property_agent__agent_info {
		padding: 0 1.5rem 1.5rem;

		.contact {
			margin-bottom: 1rem;
			line-height: 1.5;
		}
		.value {
			color: $rh_heading_color;
		}
		.email {
			.value {
				color: $rh_primary_color;
			}
		}
	}

	.rh_property_agent__link {
		display: block;
		padding: 2rem 0;
		transition: 0.2s all linear;

		&:hover {
			transition: 0.2s all linear;
		}
	}
}

.multiple-agent {
	margin-top: 7rem;

	@include r(320)  { margin-top: 0; }
	@include r(480)  { margin-top: 0; }
	@include r(768)  { margin-top: 0; }
	@include r(1024) { margin-top: 7rem; }
	@include r(1140) { margin-top: 7rem; }
	@include r(1280) { margin-top: 7rem; }
}

.rh_sidebar {
	.rh_property_agent {
		.rh_property_agent__link {
			&:hover {
				color: #fff;
			}
		}
	}
}
