/**
 * Shortcode Styles
 *
 */

.list-container {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	margin-left: -1.5rem;
	margin-right: -1.5rem;

	.rh_list_card {
		max-width: 80rem;
	}

	.rh_list_card__wrap .rh_list_card__details {
		border-bottom: none;

		@include r(320)  { width: 100%; padding: 0; border-right: none; }
		@include r(480)  { width: 100%; padding: 0; border-right: none; }
		@include r(768)  { width: 100%; padding: 0; border-right: none; }
		@include r(1024) { width: 100%; padding: 0; border-right: none; }
		@include r(1140) { width: 100%; padding: 0; border-right: none; }
		@include r(1280) { width: 65%; padding: 0 2rem 0 0; border-right: 1px solid rgba($rh_text_color, 0.2); }
	}

	.rh_list_card__wrap .rh_list_card__priceLabel {
		@include r(320)  { display: none; }
		@include r(480)  { display: none; }
		@include r(768)  { display: none; }
		@include r(1024) { display: none; }
		@include r(1140) { display: none; }
		@include r(1280) { display: flex; }
	}

	.rh_list_card__wrap .rh_list_card__thumbnail {
		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 40%; }
		@include r(1024) { width: 40%; }
		@include r(1140) { width: 40%; }
		@include r(1280) { width: 30%; }
	}

	.rh_list_card__wrap .rh_list_card__details_wrap {
		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 60%; }
		@include r(1024) { width: 60%; }
		@include r(1140) { width: 60%; }
		@include r(1280) { width: 70%; }
	}
}

.listing-layout {
	.list-container {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		margin-left: -2rem;
		margin-right: -2rem;

		@include r(320)  { margin-left: 0; margin-right: 0; }
		@include r(480)  { margin-left: 0; margin-right: 0; }
		@include r(768)  { margin-left: -2rem; margin-right: -2rem; }
		@include r(1024) { margin-left: -2rem; margin-right: -2rem; }
		@include r(1140) { margin-left: -2rem; margin-right: -2rem; }
		@include r(1280) { margin-left: -2rem; margin-right: -2rem; }
	}
}

.inspiry-shortcode {
	.list-container {
		.rh_label__property_grid .rh_label__wrap {
			line-height: 1.4;
		}
	}
}

/**
 * Testimonials Shortcodes
 */
.qe-testimonial-wrapper {
	.qe-testimonial-img {
		margin-top: 0;

		a {
			&:hover {
				.avatar {
					border: 3px solid $rh_secondary_color;
					transition: 0.2s all linear;
				}
			}

			.avatar {
				border: 3px solid $rh_primary_color;
				transition: 0.2s all linear;
			}
		}
	}

	.qe-testimonial-name {
		font-size: 1.6rem;
		font-weight: 500;
		line-height: 2;
		color: $rh_heading_color;
	}

	.qe-testimonial-byline {
		line-height: 1.5;

		a {
			color: $rh_primary_color;
			font-size: 1.4rem;
			transition: 0.2s all linear;

			&:hover {
				color: $rh_secondary_color;
				transition: 0.2s all linear;
			}
		}
	}

	.qe-testimonial-text {
		margin: 1.5rem 0;

		&:before {
			display: none;
		}

		p {
			font-size: 1.5rem;
		}
	}
}

/**
 * FAQs Shortcode
 */
.rh_content {
	.qe-faqs-filters-container {
		padding: 0;
		margin-bottom: 2.5rem;
	}
}

.qe-faqs-filters-container {
	li {
		a {
			color: $rh_primary_color;
			padding: 1rem 1.5rem;
			transition: 0.2s all linear;
			border-bottom: 2px solid;

			&:hover {
				color: $rh_secondary_color;
				transition: 0.2s all linear;
			}
		}
	}

	li.active {
		a {
			font-weight: 500;
		}
	}
}

.qe-faq-toggle {
	margin-bottom: 1.5rem;

	.qe-toggle-title {
		border: none;
		background: rgba($rh_primary_color, 0.1);
		color: $rh_heading_color;

		&:hover {
			background: rgba($rh_primary_color, 0.2);
		}

		strong {
			font-weight: 500;
		}

		.fa-plus-circle {
			color: $rh_text_color;

			&:before {
				content: "\f067";
			}
		}

		.fa-minus-circle {
			color: $rh_text_color;

			&:before {
				content: "\f068";
			}
		}
	}

	.qe-toggle-content {
		border: none;
		background: rgba($rh_primary_color, 0.1);
	}
}

.qe-faq-toggle.active {
	.qe-toggle-title {
		background: rgba($rh_primary_color, 0.2);
	}
}
