/**
 * Agent Card
 */

.rh_agent_card {
	display: block;
	width: 100%;
	margin-top: 6.5rem;
	padding: 0;

	@include r(320)  { margin-top: 4.5rem; padding: 0 0 1rem 0; }
	@include r(480)  { margin-top: 5.5rem; padding: 0 0 1rem 0; }
	@include r(768)  { margin-top: 6.5rem; padding: 0; }
	@include r(1024) { margin-top: 6.5rem; padding: 0; }
	@include r(1140) { margin-top: 6.5rem; padding: 0; }
	@include r(1280) { margin-top: 6.5rem; padding: 0; }
}

.rh_agent_card__wrap {
	display: block;
	background: #fff;
	padding: 2.5rem;

	.rh_agent_card__head {
		display: flex;
		justify-content: flex-start;
		text-align: left;
		margin-bottom: 2rem;

		@include r(320)  { flex-direction: column; align-items: center; }
		@include r(480)  { flex-direction: column; align-items: center; }
		@include r(768)  { flex-direction: row; align-items: flex-start; }
		@include r(1024) { flex-direction: row; align-items: flex-start; }
		@include r(1140) { flex-direction: row; align-items: flex-start; }
		@include r(1280) { flex-direction: row; align-items: flex-start; }

		.rh_agent_card__dp {
			margin: 0;
			margin-top: -6rem;
			width: 20%;

			@include r(320)  { width: 50%; text-align: center; }
			@include r(480)  { width: 30%; text-align: center; }
			@include r(768)  { width: 22%; text-align: left; }
			@include r(1024) { width: 23%; text-align: left; }
			@include r(1140) { width: 20%; text-align: left; }
			@include r(1280) { width: 20%; text-align: left; }
		}

		.rh_agent_card__name {
			width: 56%;
			margin-left: 2.5rem;

			@include r(320)  { width: 100%; margin-left: 0; margin-top: 2rem; text-align: center; }
			@include r(480)  { width: 100%; margin-left: 0; margin-top: 2rem; text-align: center; }
			@include r(768)  { width: 54%; margin-left: 2.5rem; margin-top: 0; text-align: left; }
			@include r(1024) { width: 53%; margin-left: 2.5rem; margin-top: 0; text-align: left; }
			@include r(1140) { width: 56%; margin-left: 2.5rem; margin-top: 0; text-align: left; }
			@include r(1280) { width: 56%; margin-left: 2.5rem; margin-top: 0; text-align: left; }

			.name {
				margin: 0;
				margin-bottom: 0.8rem;

				a {
					font-size: 2.4rem;
					font-weight: 500;
					line-height: 1.5;
					color: $rh_heading_color;
					transition: 0.2s all linear;

					&:hover {
						color: $rh_primary_color;
						transition: 0.2s all linear;
					}
				}
			}

			.social {
				a {
					display: inline-block;
					vertical-align: top;
					margin: 0 0.5rem;
					color: rgba($rh_text_color, 0.5);
					transition: 0.2s all linear;

					&:hover {
						color: $rh_secondary_color;
						transition: 0.2s all linear;
					}

					i {
						font-size: 1.8rem;
					}
				}
			}
		}

		.rh_agent_card__listings {
			width: 24%;
			padding: 0 0 0 3rem;
			border-left: 1px solid rgba($rh_text_color, 0.2);

			@include r(320)  { width: 100%; padding: 2rem 0 0 0; text-align: center; border-left: none; }
			@include r(480)  { width: 100%; padding: 2rem 0 0 0; text-align: center; border-left: none; }
			@include r(768)  { width: 24%; padding: 0 0 0 3rem; text-align: left; border-left: 1px solid rgba($rh_text_color, 0.2); }
			@include r(1024) { width: 24%; padding: 0 0 0 3rem; text-align: left; border-left: 1px solid rgba($rh_text_color, 0.2); }
			@include r(1140) { width: 24%; padding: 0 0 0 3rem; text-align: left; border-left: 1px solid rgba($rh_text_color, 0.2); }
			@include r(1280) { width: 24%; padding: 0 0 0 3rem; text-align: left; border-left: 1px solid rgba($rh_text_color, 0.2); }

			.head {
				color: $rh_heading_color;
				font-size: 1.3rem;
				margin-bottom: 0.8rem;
			}

			.count {
				font-size: 2.4rem;
				color: $rh_primary_color;
			}
		}
	}

	.rh_agent_card__details {
		text-align: left;

		.content {
			line-height: 2;
			margin-bottom: 3rem;

			@include r(320)  { margin-bottom: 2rem; }
			@include r(480)  { margin-bottom: 2rem; }
			@include r(768)  { margin-bottom: 3rem; }
			@include r(1024) { margin-bottom: 3rem; }
			@include r(1140) { margin-bottom: 3rem; }
			@include r(1280) { margin-bottom: 3rem; }
		}

		.rh_agent_card__contact {
			display: flex;
			justify-content: space-between;
			align-items: center;

			@include r(320)  { flex-direction: column; align-items: flex-start; }
			@include r(480)  { flex-direction: column; align-items: flex-start; }
			@include r(768)  { flex-direction: row; align-items: center; }
			@include r(1024) { flex-direction: row; align-items: center; }
			@include r(1140) { flex-direction: row; align-items: center; }
			@include r(1280) { flex-direction: row; align-items: center; }

			.rh_agent_card__contact_wrap {
				display: flex;
				flex-wrap: wrap;

				.contact {
					line-height: 1.5;
					margin-bottom: 1rem;
					margin-right: 1rem;
					padding: 0 1rem 0 0;

					a {
						display: inline-block;
						font-size: 1.5rem;
						color: $rh_heading_color;
						transition: 0.2s all linear;

						&:hover {
							color: $rh_primary_color;
							transition: 0.2s all linear;
						}
					}
				}
			}

			.rh_agent_card__link {
				display: flex;
				align-items: center;
				min-width: 15rem;
				transition: 0.2s all linear;

				@include r(320)  { margin-top: 1rem; }
				@include r(480)  { margin-top: 1rem; }
				@include r(768)  { margin-top: 0; }
				@include r(1024) { margin-top: 0; }
				@include r(1140) { margin-top: 0; }
				@include r(1280) { margin-top: 0; }

				&:hover {
					p, i {
						color: $rh_primary_color;
						transition: 0.2s all linear;
					}
				}

				p {
					color: $rh_secondary_color;
					transition: 0.2s all linear;
					margin-right: 2rem;
					transition: 0.2s all linear;
				}

				i {
					font-size: 1.8rem;
					color: $rh_secondary_color;
					transition: 0.2s all linear;
				}
			}
		}
	}
}
