/**
 * Labels
 */

.rh_label {
	display: inline-block;
	font-size: 1.4rem;
	background: $rh_menu_hover;
	color: #fff;

	.rh_label__wrap {
		position: relative;
		padding: 0.5rem 0.5rem 0.5rem 1rem;
	}

	span {
		display: inline-block;
	    width: 0;
	    height: 0;
	    position: absolute;
	    right: -0.9rem;
	    top: 0;
	    border: 0 solid transparent;
	    border-bottom-width: 13px;
	    border-top-width: 13px;
	    border-left: 9px solid $rh_menu_hover;
	}
}

.rh_label__slide {
	position: absolute;
	top: -13px;
    left: 0;
}

.rh_label__property {
	position: absolute;
    left: 1.5rem;
    top: -1.3rem;
    z-index: 999;

    .rh_label__wrap {
    	padding: 0.4rem 0.5rem 0.3rem 1rem;
    }
}

.rh_label__featured {
	position: absolute;
	left: 0;
	top: -1.3rem;
	z-index: 999;
}

.rh_label__list {
	position: absolute;
	left: 0;
	top: -1.3rem;
	z-index: 9;
}

.rh_label__featured_widget {
	position: absolute;
	left: 0;
	top: -1.3rem;
	z-index: 999;

	.rh_label__wrap {
    	padding: 0.3rem 0.5rem 0.2rem 1rem;
    }
}

.rh_label__property_grid {
	position: absolute;
	left: 1.5rem;
	top: -1.3rem;
	z-index: 9;

	.rh_label__wrap {
		padding: 0.4rem 0.5rem 0.3rem 1rem;
	}
}

.rh_label__favorite {
	position: absolute;
	left: 1.5rem;
	top: -1.3rem;
	z-index: 999;

	.rh_label__wrap {
		padding: 0.4rem 0.5rem 0.3rem 1rem;
	}
}
