/**
 * Page Templates
 */

.inspiry-message {
	text-align: left;
	margin-bottom: 3rem;

	strong {
		font-size: 1.6rem;
	}
}

.rh_page {
	padding: 0 0 20rem 0;

	@include r(320)  { padding: 0; }
	@include r(480)  { padding: 0; }
	@include r(768)  { padding: 0; }
	@include r(1024) { padding: 0 0 17rem 0; }
	@include r(1140) { padding: 0 0 20rem 0; }
	@include r(1280) { padding: 0 0 20rem 0; }

	.no-results {
		font-size: 1.5rem;
		font-weight: 500;
		line-height: 1.5;
	}

	.rh_alert-wrapper {
		margin: 0;
	}
}

.rh_page__listing_page {
	padding: 0 0 20rem 0;
	margin: 0 auto;

	@include r(320)  { padding: 0 0 2rem 0; max-width: 100%; }
	@include r(480)  { padding: 0 0 2rem 0; max-width: 35rem; }
	@include r(768)  { padding: 0 0 2rem 0; max-width: 100%; }
	@include r(1024) { padding: 0 0 17rem 0; max-width: 100%; }
	@include r(1140) { padding: 0 0 20rem 0; max-width: 100%; }
	@include r(1280) { padding: 0 0 20rem 0; max-width: 100%; }
}

.rh_page__main {
	width: 70%;
	padding-right: 3rem;

	@include r(320)  { width: 100%; padding-right: 0; }
	@include r(480)  { width: 100%; padding-right: 0; }
	@include r(768)  { width: 100%; padding-right: 0; }
	@include r(1024) { width: 73%; padding-right: 3rem; }
	@include r(1140) { width: 73%; padding-right: 3rem; }
	@include r(1280) { width: 70%; padding-right: 3rem; }
}

.rh_page__sidebar {
	width: 30%;
	margin: 0 auto;

	@include r(320)  { width: 100%; max-width: 35rem; }
	@include r(480)  { width: 100%; max-width: 35rem; }
	@include r(768)  { width: 60%; max-width: 100%; }
	@include r(1024) { width: 27%; max-width: 100%; }
	@include r(1140) { width: 27%; max-width: 100%; }
	@include r(1280) { width: 30%; max-width: 100%; }
}

.rh_page__listing {
	display: block;
	width: 100%;
	margin: 0 auto;
	padding: 0;
}

.rh_page__head {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: left;
	padding: 0 0 5rem 0;

	@include r(320)  { flex-direction: column; align-items: flex-start; padding: 0 0 1rem 0; }
	@include r(480)  { flex-direction: column; align-items: flex-start; padding: 0 0 2rem 0; }
	@include r(768)  { flex-direction: row; align-items: center; padding: 0 0 5rem 0; }
	@include r(1024) { flex-direction: row; align-items: center; padding: 0 0 5rem 0; }
	@include r(1140) { flex-direction: row; align-items: center; padding: 0 0 5rem 0; }
	@include r(1280) { flex-direction: row; align-items: center; padding: 0 0 5rem 0; }

	.rh_page__title {
		display: flex;
		flex-direction: column;
		flex: 1 1 auto;
		margin: 0;
		padding: 0;

		@include r(320)  { padding: 0 0 1rem 0; }
		@include r(480)  { padding: 0 0 1rem 0; }
		@include r(768)  { padding: 0; }
		@include r(1024) { padding: 0; }
		@include r(1140) { padding: 0; }
		@include r(1280) { padding: 0; }

		.sub {
			color: $rh_primary_color;
			font-size: 1.5rem;
			font-weight: 500;
		}

		.title {
			color: $rh_heading_color;
			font-size: 3.6rem;
		}
	}

	.rh_page__title_pad {
		padding: 0;

		@include r(320)  { padding: 0 0 2rem 0; }
		@include r(480)  { padding: 0 0 2rem 0; }
		@include r(768)  { padding: 0; }
		@include r(1024) { padding: 0; }
		@include r(1140) { padding: 0; }
		@include r(1280) { padding: 0; }
	}

	.rh_page__gallery_filters {
		@include r(320)  { padding: 0 0 3rem 0; }
		@include r(480)  { padding: 0 0 2rem 0; }
		@include r(768)  { padding: 0; }
		@include r(1024) { padding: 0; }
		@include r(1140) { padding: 0; }
		@include r(1280) { padding: 0; }
	}

	.rh_page__nav {
		display: flex;
		flex: 1 1 auto;
		justify-content: flex-end;
		text-align: right;
		padding: 0;

		@include r(320)  { flex-direction: column; padding: 0 0 1rem 0; }
		@include r(480)  { flex-direction: row; padding: 0 0 1rem 0; }
		@include r(768)  { flex-direction: row; padding: 0; }
		@include r(1024) { flex-direction: row; padding: 0; }
		@include r(1140) { flex-direction: row; padding: 0; }
		@include r(1280) { flex-direction: row; padding: 0; }

		.rh_page__nav_item {
			display: flex;
			align-items: center;
			padding: 1.5rem;
			border-bottom: 3px solid transparent;
			transition: 0.2s all linear;

			&:hover {
				border-bottom: 3px solid $rh_primary_color;
				transition: 0.2s all linear;

				svg {
					path {
						fill: $rh_heading_color;
					}
				}

				p {
					color: $rh_heading_color;
				}
			}

			svg {
				path {
					fill: $rh_text_color;
				}
			}

			p {
				display: inline-block;
				margin-left: 1rem;
			}
		}

		.active {
			border-bottom: 3px solid $rh_primary_color;

			svg {
				path {
					fill: $rh_heading_color;
				}
			}

			p {
				color: $rh_heading_color;
			}
		}
	}

	.rh_page__controls {
		display: flex;
		flex: 1 1 auto;
		justify-content: flex-end;
		text-align: right;

		@include r(320)  { flex-direction: column; padding: 0 0 1rem 0; }
		@include r(480)  { flex-direction: row; justify-content: space-between; width: 100%; padding: 0 0 1rem 0; }
		@include r(768)  { flex-direction: row; justify-content: flex-end; width: initial; padding: 0; }
		@include r(1024) { flex-direction: row; justify-content: flex-end; width: initial; padding: 0; }
		@include r(1140) { flex-direction: row; justify-content: flex-end; width: initial; padding: 0; }
		@include r(1280) { flex-direction: row; justify-content: flex-end; width: initial; padding: 0; }

		.rh_sort_controls {
			position: relative;
			-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
			-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
			box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

			select {
				background: #fff;
				border: 1px solid rgba($rh_text_color, 0.2);
				border-radius: 0;
				color: $rh_text_color;
    			padding: 1.5rem 2rem;
    			outline: none;
    			width: 22rem;

    			&:focus {
    				outline: none;
    			}
			}

			.select2-selection--single {
				border: 1px solid rgba($rh_text_color, 0.2);
				border-radius: 0;
				color: $rh_text_color;
    			padding: 1rem 2rem;
    			outline: none;
    			width: 22rem;
    			height: initial;
    			text-align: left;
			}

			.select2-selection__arrow {
				top: 27%;
				top: calc(50% - 13px);
    			right: 1rem;
			}

			.select2-selection__rendered {
				color: $rh_text_color;
				padding-left: 0;
				padding-right: 1rem;
			}

			// &:after {
			// 	content: '';
			// 	position: absolute;
			// 	top: 50%;
			// 	right: 1.5rem;
			// 	margin-top: -2.6px;
			// 	border: 0 solid transparent;
			// 	border-left-width: 5px;
			// 	border-right-width: 5px;
			// 	border-top: 5px solid rgba($rh_text_color, 0.5);
			// }
		}
	}
}

.rh_view_type {
	display: flex;
	align-items: center;

	a {
		display: inline-block;
		margin: 0 0 0 2.5rem;

		@include r(320)  { margin: 2rem 3rem 0 0; }
		@include r(480)  { margin: 0 0 0 2.5rem; }
		@include r(768)  { margin: 0 0 0 2.5rem; }
		@include r(1024) { margin: 0 0 0 2.5rem; }
		@include r(1140) { margin: 0 0 0 2.5rem; }
		@include r(1280) { margin: 0 0 0 2.5rem; }

		&:hover {
			svg {
				path {
					fill: $rh_secondary_color;
					transition: 0.2s all linear;
				}
			}
		}

		svg {
			path {
				fill: $rh_primary_color;
				transition: 0.2s all linear;
			}
		}
	}

	a.active {
		svg {
			path {
				fill: $rh_secondary_color;
			}
		}
	}
}

.rh_page__listing_grid {
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	width: initial;
	margin: 0 -1.5rem;
}

.rh_page__listing_map {
	display: flex;
	width: 50%;
	height: 100%;
	padding: 0;

	@include r(320)  { width: 100%; height: 50rem; padding: 0; }
	@include r(480)  { width: 100%; height: 50rem; padding: 0; }
	@include r(768)  { width: 100%; height: 50rem; padding: 0; }
	@include r(1024) { width: 100%; height: 50rem; padding: 0; }
	@include r(1140) { width: 50%; height: initial; padding: 0; }
	@include r(1280) { width: 50%; height: initial; padding: 0; }

	#map-head {
		display: flex;
		width: 100%;
	}

	#listing-map {
		width: 100%;
	}
}

.rh_page__news {
	max-width: 100%;
	@include r(480) { max-width: 100%; }
}

.rh_page__map_properties {
	width: 50%;
	padding: 10rem 10rem 15rem 3rem;
	max-width: 720px;
	margin: 0 auto 0 0;

	@include r(320)  { width: 100%; padding: 2rem 2rem 3rem 2rem; margin: 0 auto; max-width: 400px; }
	@include r(480)  { width: 100%; padding: 3rem; margin: 0 auto; max-width: 410px; }
	@include r(768)  { width: 100%; padding: 5rem 5rem 3rem 5rem; margin: 0 auto; max-width: 720px; }
	@include r(1024) { width: 100%; padding: 5rem 5rem 15rem 5rem; margin: 0 auto; max-width: 720px; }
	@include r(1140) { width: 50%; padding: 10rem 7rem 15rem 3rem; margin: 0 auto 0 0; max-width: 720px; }
	@include r(1280) { width: 50%; padding: 10rem 10rem 15rem 3rem; margin: 0 auto 0 0; max-width: 720px; }

	.rh_list_card {
		max-width: 55rem;

		@include r(320)  { max-width: 40rem; }
		@include r(480)  { max-width: 35rem; }
		@include r(768)  { max-width: 100%; }
		@include r(1024) { max-width: 100%; }
		@include r(1140) { max-width: 100%; }
		@include r(1280) { max-width: 100%; }
	}
}

.rh_page--agents_listing {
	padding: 0 0 2rem 0;

	@include r(320)  { padding: 0; }
	@include r(480)  { padding: 0; }
	@include r(768)  { padding: 0; }
	@include r(1024) { padding: 0 0 2rem 0; }
	@include r(1140) { padding: 0 0 2rem 0; }
	@include r(1280) { padding: 0 0 2rem 0; }
}

.rh_page__agents {
	@include r(320)  { padding: 0; }
	@include r(480)  { padding: 0 0 2rem 0; }
	@include r(768)  { padding: 0 0 3rem 0; }
	@include r(1024) { padding: 0 3rem 17rem 0; }
	@include r(1140) { padding: 0 3rem 20rem 0; }
	@include r(1280) { padding: 0 3rem 20rem 0; }
}

.rh_page--single_agent {
	padding: 3rem 0;
}

.rh_page--fullWidth {
	width: 100%;
}

.rh_page__property {
	justify-content: space-between;
	align-items: center;

	@include r(320)  { flex-direction: column; padding: 0 0 2rem 0; }
	@include r(480)  { flex-direction: column; padding: 0 0 3rem 0; }
	@include r(768)  { flex-direction: column; padding: 0 0 5rem 0; }
	@include r(1024) { flex-direction: row; padding: 0 0 5rem 0; }
	@include r(1140) { flex-direction: row; padding: 0 0 5rem 0; }
	@include r(1280) { flex-direction: row; padding: 0 0 5rem 0; }

	.rh_page__property_title {
		@include r(320)  { text-align: center; }
		@include r(480)  { text-align: center; }
		@include r(768)  { text-align: center; }
		@include r(1024) { text-align: left; }
		@include r(1140) { text-align: left; }
		@include r(1280) { text-align: left; }

		.rh_page__title {
			font-size: 2.4rem;
			font-weight: 500;
			line-height: 1.5;
			margin-bottom: 0.5rem;
			padding: 0;

			@include r(320)  { margin-bottom: 0; }
			@include r(480)  { margin-bottom: 0.5rem; }
			@include r(768)  { margin-bottom: 0.5rem; }
			@include r(1024) { margin-bottom: 0.5rem; }
			@include r(1140) { margin-bottom: 0.5rem; }
			@include r(1280) { margin-bottom: 0.5rem; }
		}

		.rh_page__property_address {
			font-size: 1.5rem;
			line-height: 1.5;
		}
	}

	.rh_page__property_price {
		padding: 0 0 0 2rem;
		border-left: 1px solid rgba($rh_text_color, 0.2);
		border-left-style: dotted;

		@include r(320)  { text-align: center; padding: 2rem 0 0 0; border-left: none; }
		@include r(480)  { text-align: center; padding: 2.5rem 0 0 0; border-left: none; }
		@include r(768)  { text-align: center; padding: 2rem 0 0 0; border-left: none; }
		@include r(1024) { text-align: left; padding: 0 0 0 2rem; border-left: 1px solid rgba($rh_text_color, 0.2); }
		@include r(1140) { text-align: left; padding: 0 0 0 2rem; border-left: 1px solid rgba($rh_text_color, 0.2); }
		@include r(1280) { text-align: left; padding: 0 0 0 2rem; border-left: 1px solid rgba($rh_text_color, 0.2); }

		.status {
			font-size: 1.5rem;
			color: $rh_heading_color;
		}

		.price {
			font-size: 2.4rem;
			line-height: 1.5;
			color: $rh_primary_color;
		}
	}
}

.rh_page__favorites {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -1.5rem;

	.rh_alert-wrapper {
		margin: 0 1.5rem;
	}
}

.rh_alert-wrapper {
	margin: 0 1.5rem;
	text-align: left;

	.no-results {
		font-size: 1.5rem;
		font-weight: 500;
		line-height: 2;
		margin: 0;
	}
}

.rh_page__section {
	.rh_alert-wrapper {
		margin: 0;
	}
}

.rh_alert__404 {
	@include r(320)  { margin: 0 0 2rem 0; }
	@include r(480)  { margin: 0 0 3rem 0; }
	@include r(768)  { margin: 0 0 5rem 0; }
	@include r(1024) { margin: 0; }
	@include r(1140) { margin: 0; }
	@include r(1280) { margin: 0; }
}

.rh_alert__widget {
	margin: 0;
}

.rh_page__gallery_filters {
	a {
		display: inline-block;
		padding: 1.5rem;
		transition: 0.2s all linear;
		border-bottom: 3px solid rgba(#fff, 0);

		&:hover {
			color: $rh_option_active;
			border-bottom: 3px solid $rh_primary_color;
			transition: 0.2s all linear;
		}
	}
	a.active {
		color: $rh_option_active;
		border-bottom: 3px solid $rh_primary_color;
	}
}

.rh_page__contact {
	background: #fff;

	.rh_blog {
		padding: 2rem 0 0 0;

		@include r(320)  { padding: 0; }
		@include r(480)  { padding: 0; }
		@include r(768)  { padding: 2rem 0 0 0; }
		@include r(1024) { padding: 2rem 0 0 0; }
		@include r(1140) { padding: 2rem 0 0 0; }
		@include r(1280) { padding: 2rem 0 0 0; }
	}

	.entry-content {
		@include r(320)  { padding: 2rem 2.5rem 0; margin: 0; }
		@include r(480)  { padding: 2rem 2.5rem 0; margin: 0; }
		@include r(768)  { padding: 2rem 3rem; margin: 0 2rem; }
		@include r(1024) { padding: 2rem 3rem; margin: 0 2rem; }
		@include r(1140) { padding: 2rem 3rem; margin: 0 2rem; }
		@include r(1280) { padding: 2rem 3rem; margin: 0 2rem; }
	}
}

.page-template-template-fullwidth {
	.rh_wrap--padding {
	    @include r(320)  { padding: 2rem 2rem 2rem; }
	    @include r(480)  { padding: 3rem 3rem 3rem; }
	    @include r(768)  { padding: 5rem 5rem 5rem; }
	    @include r(1024) { padding: 5rem 5rem 5rem; }
	    @include r(1140) { padding: 5rem 7rem; }
	    @include r(1280) { padding: 5rem 10rem; }
	}
}
