/**
 * Pagination
 */

.rh_pagination {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin: 2rem 0;

	@include r(320)  { margin: 1rem 0; }
	@include r(480)  { margin: 2rem 0; }
	@include r(768)  { margin: 2rem 0; }
	@include r(1024) { margin: 2rem 0; }
	@include r(1140) { margin: 2rem 0; }
	@include r(1280) { margin: 2rem 0; }

	a {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 1rem;
		color: $rh_text_color;
		background: #fff;
		border-radius: 500rem;
		width: 4rem;
		height: 4rem;
		font-size: 1.4rem;
		font-weight: 500;
		transition: 0.2s all linear;
		-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
	}

	.rh_pagination__btn {
		&:hover {
			background: $rh_primary_color;
			color: #fff;
			transition: 0.2s all linear;
			-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
			-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
			box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		}
	}

	.current {
		color: #fff;
		background: $rh_primary_color;
	}
}

.rh_pagination__prev,
.rh_pagination__next {
	svg {
		fill: $rh_text_color;
		width: 23px;
	}

	&:hover {
		svg {
			fill: #fff;
		}
	}
}

.rh_pagination__pages-nav {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 0 3rem 0;
	padding: 0 0 5rem 0;

	@include r(320)  { padding: 0 0 2.5rem 0; }
	@include r(480)  { padding: 0 0 3rem 0; }
	@include r(768)  { padding: 0 0 5rem 0; }
	@include r(1024) { padding: 0 0 5rem 0; }
	@include r(1140) { padding: 0 0 5rem 0; }
	@include r(1280) { padding: 0 0 5rem 0; }

	a {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 1rem;
		padding: 1.5rem;
		color: #fff;
		font-size: 1.4rem;
		font-weight: 500;
		background: $rh_primary_color;
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		transition: 0.2s all linear;

		&:hover {
			background: darken($rh_primary_color, 5%);
			transition: 0.2s all linear;
		}
	}
}
