
/**
 * Section Testimonial
 */

.rh_section__testimonial {
	background: #1ea69a;
	padding: 10rem;
	position: relative;

	@include r(320)  { padding: 5rem 2rem; }
	@include r(480)  { padding: 7rem 3rem; }
	@include r(768)  { padding: 7rem; }
	@include r(1024) { padding: 18rem 7rem; }
	@include r(1140) { padding: 18rem 7rem; }
	@include r(1280) { padding: 18rem 10rem; }

	&:before {
		content: '';
		color: transparent;
	    width: 0;
	    height: 0;
	    border: 0 solid transparent;
	    border-right-width: 0px;
	    border-left-width: 1440px;
	    border-left-width: 100vw;
	    border-bottom: 180px solid $rh_primary_color;
	    position: absolute;
	    top: -90px;
	    left: 0;
	    z-index: 99;

	    @include r(320)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(480)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(768)  { border-left-width: 0; border-bottom-width: 0; }
		@include r(1024) { top: -70px; border-left-width: 100vw; border-bottom-width: 140px; }
		@include r(1140) { top: -80px; border-left-width: 100vw; border-bottom-width: 160px; }
		@include r(1280) { top: -90px; border-left-width: 100vw; border-bottom-width: 180px; }
	}

	&:after {
		content: '';
	    width: 0;
	    height: 0;
	    border: 0 solid transparent;
	    border-left-width: 0px;
	    border-right-width: 1440px;
	    border-right-width: 100vw;
	    border-top: 180px solid #1ea69a;
	    position: absolute;
	    bottom: -90px;
	    left: 0;
	    z-index: 9999;

	    @include r(320)  { border-right-width: 0; border-top-width: 0; }
		@include r(480)  { border-right-width: 0; border-top-width: 0; }
		@include r(768)  { border-right-width: 0; border-top-width: 0; }
		@include r(1024) { bottom: -50px; border-right-width: 100vw; border-top-width: 140px; }
		@include r(1140) { bottom: -70px; border-right-width: 100vw; border-top-width: 160px; }
		@include r(1280) { bottom: -90px; border-right-width: 100vw; border-top-width: 180px; }
	}
}

.rh_testimonial {
	max-width: 95rem;
    text-align: center;
    margin: 0 auto;
    position: relative;
    z-index: 99999;

    .rh_testimonial__quote {
    	position: relative;
    	z-index: 999;
    	font-size: 3.6rem;
    	font-weight: 400;
    	line-height: 1.5;
    	color: #fff;
    	margin: 0;
    	margin-bottom: 5rem;

    	@include r(320)  { font-size: 2.2rem; margin-bottom: 2rem; }
		@include r(480)  { font-size: 2.5rem; margin-bottom: 3rem; }
		@include r(768)  { font-size: 2.7rem; margin-bottom: 5rem; }
		@include r(1024) { font-size: 3.2rem; margin-bottom: 5rem; }
		@include r(1140) { font-size: 3.2rem; margin-bottom: 5rem; }
		@include r(1280) { font-size: 3.6rem; margin-bottom: 5rem; }
    }

    .rh_testimonial__author {
    	display: flex;
    	justify-content: center;
    	align-items: center;
    	position: relative;
    	z-index: 999;

    	@include r(320)  { flex-direction: column; }
		@include r(480)  { flex-direction: column; }
		@include r(768)  { flex-direction: row; }
		@include r(1024) { flex-direction: row; }
		@include r(1140) { flex-direction: row; }
		@include r(1280) { flex-direction: row; }

    	.rh_testimonial__author_name {
    		color: #fff;
    		font-size: 3rem;
    		font-weight: 500;
    		padding: 0 1rem 0 0;

    		@include r(320)  { font-size: 2rem; padding: 0 0 1rem 0; }
			@include r(480)  { font-size: 2.2rem; padding: 0 0 1rem 0; }
			@include r(768)  { font-size: 2.5rem; padding: 0 1rem 0 0; }
			@include r(1024) { font-size: 3rem; padding: 0 1rem 0 0; }
			@include r(1140) { font-size: 3rem; padding: 0 1rem 0 0; }
			@include r(1280) { font-size: 3rem; padding: 0 1rem 0 0; }
    	}

    	.rh_testimonial__author__link {
    		a {
    			color: #fff;
    			font-size: 1.8rem;
    			text-decoration: underline;
    		}
    	}
    }
}

.rh_testimonial__quote_bg {
	background: url( '../../images/icons/quote-left.png' ) 10% 0% no-repeat,
				url( '../../images/icons/quote-right.png' ) 90% 100% no-repeat;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	height: calc(100% + -3rem);
    margin-top: 1.5rem;
    opacity: 0.8;
    z-index: 99999;

	@include r(320)  { display: none; }
	@include r(480)  { display: none; }
	@include r(768)  { display: none; }
	@include r(1024) {
		display: block;
		background: url( '../../images/icons/quote-left.png' ) 5% 0% no-repeat,
					url( '../../images/icons/quote-right.png' ) 95% 100% no-repeat;
	}
	@include r(1140) {
		display: block;
		background: url( '../../images/icons/quote-left.png' ) 5% 0% no-repeat,
					url( '../../images/icons/quote-right.png' ) 95% 100% no-repeat;
	}
	@include r(1280) {
		display: block;
		background: url( '../../images/icons/quote-left.png' ) 10% 0% no-repeat,
					url( '../../images/icons/quote-right.png' ) 90% 100% no-repeat;
	}
}
