/**
 * Single Property Template
 */

.rh_property {
	display: flex;
	flex-direction: column;

	#property-detail-flexslider {
		margin-bottom: 3rem;
	}

	.rh_property__row {
		display: flex;
		flex-direction: row;
		margin-bottom: 3rem;

		@include r(320)  { margin-bottom: 2rem; flex-direction: column; }
		@include r(480)  { margin-bottom: 3rem; flex-direction: row; }
		@include r(768)  { margin-bottom: 3rem; flex-direction: row; }
		@include r(1024) { margin-bottom: 3rem; flex-direction: row; }
		@include r(1140) { margin-bottom: 3rem; flex-direction: row; }
		@include r(1280) { margin-bottom: 3rem; flex-direction: row; }
	}

	.rh_property__meta_wrap {
		flex-direction: row;
		flex-wrap: wrap;
		margin-bottom: 0;
	}

	.rh_property__wrap {
		display: flex;
		flex-direction: row;

		@include r(320)  { flex-direction: column; }
		@include r(480)  { flex-direction: column; }
		@include r(768)  { flex-direction: column; }
		@include r(1024) { flex-direction: row; }
		@include r(1140) { flex-direction: row; }
		@include r(1280) { flex-direction: row; }
	}

	.rh_property__meta {
		align-items: center;
		justify-content: space-between;
	}

	.rh_property--borderBottom {
		padding: 0 0 3rem 0;
		border-bottom: 1px dashed rgba($rh_text_color, 0.2);

		@include r(320)  { padding: 0 0 2rem 0; }
		@include r(480)  { padding: 0 0 3rem 0; }
		@include r(768)  { padding: 0 0 3rem 0; }
		@include r(1024) { padding: 0 0 3rem 0; }
		@include r(1140) { padding: 0 0 3rem 0; }
		@include r(1280) { padding: 0 0 3rem 0; }
	}

	.rh_property__main {
		width: 70%;
		margin-right: 3rem;

		@include r(320)  { width: 100%; margin-right: 0; }
		@include r(480)  { width: 100%; margin-right: 0; }
		@include r(768)  { width: 100%; margin-right: 0; }
		@include r(1024) { width: 70%; margin-right: 3rem; }
		@include r(1140) { width: 70%; margin-right: 3rem; }
		@include r(1280) { width: 70%; margin-right: 3rem; }
	}

	.rh_property__content {
		margin-bottom: 3rem;
		padding: 3rem;
		background: #fff;
		text-align: left;
		-webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		-moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);
		box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.05);

		@include r(320)  { padding: 2rem; margin-bottom: 2rem; }
		@include r(480)  { padding: 3rem; margin-bottom: 3rem; }
		@include r(768)  { padding: 3rem; margin-bottom: 3rem; }
		@include r(1024) { padding: 3rem; margin-bottom: 3rem; }
		@include r(1140) { padding: 3rem; margin-bottom: 3rem; }
		@include r(1280) { padding: 3rem; margin-bottom: 3rem; }
	}

	.rh_property__sidebar {
		width: 30%;
		max-width: 40rem;
		margin: 0 auto;
		margin-bottom: 3rem;

		@include r(320)  { width: 100%; margin-top: 4rem; }
		@include r(480)  { width: 100%; margin-top: 4rem; }
		@include r(768)  { width: 100%; margin-top: 4rem; }
		@include r(1024) { width: 30%; margin-top: 0; }
		@include r(1140) { width: 30%; margin-top: 0; }
		@include r(1280) { width: 30%; margin-top: 0; }
	}

	// .rh_content {
	// 	margin-bottom: 3rem;

	// 	@include r(320)  { margin-bottom: 2rem; }
	// 	@include r(480)  { margin-bottom: 3rem; }
	// 	@include r(768)  { margin-bottom: 3rem; }
	// 	@include r(1024) { margin-bottom: 3rem; }
	// 	@include r(1140) { margin-bottom: 3rem; }
	// 	@include r(1280) { margin-bottom: 3rem; }
	// }
}

.rh_property--padding {
	padding: 5rem 0 0 0;

	@include r(320)  { padding: 2rem 0 0 0; }
	@include r(480)  { padding: 3rem 0 0 0; }
	@include r(768)  { padding: 5rem 0 0 0; }
	@include r(1024) { padding: 5rem 0 0 0; }
	@include r(1140) { padding: 5rem 0 0 0; }
	@include r(1280) { padding: 5rem 0 0 0; }
}

#property-featured-image {
	display: block;
	width: 100%;
	padding: 0;
	margin-bottom: 3rem;

	img {
		width: 100%;
	}
}

#property-detail-flexslider {

	.flexslider {
		margin: 0;
		border: none;

		.slides {
		}

		ul.flex-direction-nav {
			a.flex-prev {
				background: #fff;
				width: 40px;
				height: 60px;
				opacity: 1;
				left: 2rem;

				@include r(320)  { margin-top: -25px; width: 30px; height: 50px; left: 0 }
				@include r(480)  { margin-top: -30px; width: 40px; height: 60px; left: 0 }
				@include r(768)  { margin-top: -30px; width: 40px; height: 60px; left: 2rem }
				@include r(1024) { margin-top: -30px; width: 40px; height: 60px; left: 2rem }
				@include r(1140) { margin-top: -30px; width: 40px; height: 60px; left: 2rem }
				@include r(1280) { margin-top: -30px; width: 40px; height: 60px; left: 2rem }

				&:before {
					background: url(../../images/gallery-left-arrow.png) 50% 50% no-repeat;
					width: 40px;
					height: 60px;

					@include r(320)  { width: 30px; height: 50px; }
					@include r(480)  { width: 40px; height: 60px; }
					@include r(768)  { width: 40px; height: 60px; }
					@include r(1024) { width: 40px; height: 60px; }
					@include r(1140) { width: 40px; height: 60px; }
					@include r(1280) { width: 40px; height: 60px; }
				}

				&:hover {
					background: #000;

					&:before {
						background: url(../../images/gallery-left-arrow-hover.png) 50% 50% no-repeat;
					}
				}
			}
			a.flex-next {
				background: #fff;
				width: 40px;
				height: 60px;
				opacity: 1;
				right: 2rem;

				@include r(320)  { margin-top: -25px; width: 30px; height: 50px; right: 0; }
				@include r(480)  { margin-top: -30px; width: 40px; height: 60px; right: 0; }
				@include r(768)  { margin-top: -30px; width: 40px; height: 60px; right: 2rem; }
				@include r(1024) { margin-top: -30px; width: 40px; height: 60px; right: 2rem; }
				@include r(1140) { margin-top: -30px; width: 40px; height: 60px; right: 2rem; }
				@include r(1280) { margin-top: -30px; width: 40px; height: 60px; right: 2rem; }

				&:before {
					background: url(../../images/gallery-right-arrow.png) 50% 50% no-repeat;
					width: 40px;
					height: 60px;

					@include r(320)  { width: 30px; height: 50px; }
					@include r(480)  { width: 40px; height: 60px; }
					@include r(768)  { width: 40px; height: 60px; }
					@include r(1024) { width: 40px; height: 60px; }
					@include r(1140) { width: 40px; height: 60px; }
					@include r(1280) { width: 40px; height: 60px; }
				}

				&:hover {
					background: #000;

					&:before {
						background: url(../../images/gallery-right-arrow-hover.png) 50% 50% no-repeat;
					}
				}
			}
		}
	}
}

.rh_property__id {
	display: flex;

	.title {
		color: $rh_heading_color;
	}
	.id {
		color: $rh_primary_color;
	}
}

.rh_property__print {
	display: flex;
	position: relative;

	a {
		display: inline-block;
		width: 24px;
		height: 24px;
		margin-left: 2rem;
		color: rgba($rh_text_color, 0.5);
		position: relative;
		text-align: center;

		@include r(320)  { margin: 2rem 2rem 0 0; }
		@include r(480)  { margin: 0 0 0 2rem; }
		@include r(768)  { margin: 0 0 0 2rem; }
		@include r(1024) { margin: 0 0 0 2rem; }
		@include r(1140) { margin: 0 0 0 2rem; }
		@include r(1280) { margin: 0 0 0 2rem; }

		svg {
			path {
				fill: rgba($rh_text_color, 0.5);
				transition: 0.2s all linear;
			}
		}

		&:hover {
			svg {
				path {
					fill: #000;
					transition: 0.2s all linear;
				}
			}

			.label {
				opacity: 1;
				visibility: visible;
				transition: 0.3s all linear;
			}
		}
	}

	.highlight {
		svg {
			path {
				fill: #000;
				transition: 0.2s all linear;
			}
		}
	}

	.share {
		transform: rotateY(180deg);
	}

	.share-this.sharer-0 {
		width: 0;
	    position: absolute;
		left: 12px;
		top: 1.5rem;

		@include r(320)  { top: 3.5rem; }
		@include r(480)  { top: 1.5rem; }
		@include r(768)  { top: 1.5rem; }
		@include r(1024) { top: 1.5rem; }
		@include r(1140) { top: 1.5rem; }
		@include r(1280) { top: 1.5rem; }

		label {
			display: none;
		}

		.social {
			ul {
				li {
					width: 50px;
				}
			}

			li[class*=pinterest] {
				background: #bd081c;
			}
			li[class*=twitter] {
				background: #1da1f2;
			}
			li[class*=gplus] {
				background: #dd4b39;
			}
		}

		.social {
			position: relative;
			z-index: 999;
		}
	}

	.share-this.sharer-0 .social.networks-5.center {
		@include r(320)  { margin-left: -125px; }
		@include r(480)  { margin-left: -155px; }
		@include r(768)  { margin-left: -105px; }
		@include r(1024) { margin-left: -105px; }
		@include r(1140) { margin-left: -105px; }
		@include r(1280) { margin-left: -105px; }
	}

	.favorite-placeholder {
		display: inline-block;
		width: 24px;
		height: 24px;
		margin-left: 2rem;
		color: #000;
		position: relative;
		text-align: center;

		@include r(320)  { margin: 2rem 2rem 0 0; }
		@include r(480)  { margin: 0 0 0 2rem; }
		@include r(768)  { margin: 0 0 0 2rem; }
		@include r(1024) { margin: 0 0 0 2rem; }
		@include r(1140) { margin: 0 0 0 2rem; }
		@include r(1280) { margin: 0 0 0 2rem; }
	}

	.hide {
		display: none;
	}
}

.rh_property__meta_wrap {
	justify-content: flex-start;

	.rh_property__meta {
		margin-right: 2rem;
		margin-bottom: 2rem;
		text-align: left;

		@include r(320)  { margin-bottom: 2rem; }
		@include r(480)  { margin-bottom: 3rem; }
		@include r(768)  { margin-bottom: 3rem; }
		@include r(1024) { margin-bottom: 3rem; }
		@include r(1140) { margin-bottom: 3rem; }
		@include r(1280) { margin-bottom: 3rem; }

		h4 {
			margin: 0;
			margin-bottom: 1.5rem;
			font-weight: 400;
			font-size: 1.3rem;
		}

		div {
			display: flex;
			align-items: center;
		}

		.figure {
			margin-left: 1.5rem;
			color: $rh_heading_color;
			font-size: 1.4rem;
		}

		.label {
			margin-left: 0.5rem;
			color: $rh_text_color;
			font-size: 1.4rem;
		}
	}
}

.rh_property__heading {
	display: block;
	margin: 0;
	margin-bottom: 2rem;
	font-size: 1.6rem;
	font-weight: 500;
	line-height: 1.5;
	color: $rh_primary_color;
}

.rh_property__additional {
	display: block;
	width: 100%;
	list-style-type: none;
	font-size: 1.5rem;
	margin-bottom: 3rem;

	li {
		line-height: 2;
		padding: 0.7rem 2rem;

		@include r(320)  { padding: 0.7rem 1rem; }
		@include r(480)  { padding: 0.7rem 2rem; }
		@include r(768)  { padding: 0.7rem 2rem; }
		@include r(1024) { padding: 0.7rem 2rem; }
		@include r(1140) { padding: 0.7rem 2rem; }
		@include r(1280) { padding: 0.7rem 2rem; }

		&:nth-child(2n) {
			background: $rh_body_bg;
		}

		span {
			font-size: 1.5rem;
		}

		.title {
			color: $rh_heading_color;
			margin-right: 0.5rem;
		}
	}
}

.rh_property__features_wrap {
	display: block;
	margin-bottom: 1rem;

	.rh_property__features {
		display: flex;
		flex-wrap: wrap;
		list-style-type: none;

		@include r(320)  { flex-direction: column; }
		@include r(480)  { flex-direction: row; }
		@include r(768)  { flex-direction: row; }
		@include r(1024) { flex-direction: row; }
		@include r(1140) { flex-direction: row; }
		@include r(1280) { flex-direction: row; }
	}

	.rh_property__feature {
		display: flex;
		align-items: center;
		flex-basis: 50%;
		position: relative;
		padding: 0 2rem 2rem;

		&:before {
			content: '';
			display: inline-block;
			background: url('../../images/icons/icon-tick.png') 50% 50% no-repeat;
			position: absolute;
			left: 0;
		    top: 0;
		    width: 20px;
		    height: 20px;

		    @include r(320)  { width: 15px; height: 15px; }
			@include r(480)  { width: 20px; height: 20px; }
			@include r(768)  { width: 20px; height: 20px; }
			@include r(1024) { width: 20px; height: 20px; }
			@include r(1140) { width: 20px; height: 20px; }
			@include r(1280) { width: 20px; height: 20px; }
		}

		a {
			display: inline-block;
			margin-left: 1rem;
			line-height: 1.4;
			transition: 0.1s all linear;

			&:hover {
				transition: 0.1s all linear;
			}
		}
	}
}

.rh_property__attachments_wrap {
	display: block;
	margin-bottom: 1rem;

	.rh_property__attachments {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		list-style-type: none;

		@include r(320)  { flex-direction: column; align-items: flex-start; }
		@include r(480)  { flex-direction: row; align-items: center; }
		@include r(768)  { flex-direction: row; align-items: center; }
		@include r(1024) { flex-direction: row; align-items: center; }
		@include r(1140) { flex-direction: row; align-items: center; }
		@include r(1280) { flex-direction: row; align-items: center; }

		li {
			flex-basis: 50%;
			padding: 0 0 2rem 0;

			a {
				display: flex;
				align-items: center;
				color: $rh_heading_color;
			}

			i {
				display: inline-block;
				font-size: 5rem;
				margin-right: 1.5rem;
			}
		}
	}
}


.rh_property__floor_plans {
	display: block;
	margin-bottom: 1.5rem;
}

.floor-plans-accordions {
	display: flex;
	flex-direction: column;

	.floor-plan {
		display: flex;
		flex-direction: column;
		margin-bottom: 1.5rem;
		background: $rh_body_bg;
	}

	.floor-plan-title {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 2rem;
		border: 1px solid rgba($rh_text_color, 0.2);
		cursor: pointer;

		@include r(320)  { flex-direction: column; align-items: center; padding: 2rem 1rem; }
		@include r(480)  { flex-direction: column; align-items: center; padding: 2rem 1rem; }
		@include r(768)  { flex-direction: row; align-items: flex-start; padding: 2rem 1rem; }
		@include r(1024) { flex-direction: row; align-items: flex-start; padding: 2rem; }
		@include r(1140) { flex-direction: row; align-items: flex-start; padding: 2rem; }
		@include r(1280) { flex-direction: row; align-items: flex-start; padding: 2rem; }

		.title {
			display: flex;
			align-items: center;

			@include r(320)  { margin-bottom: 0.5rem; }
			@include r(480)  { margin-bottom: 0.5rem; }
			@include r(768)  { margin-bottom: 0; }
			@include r(1024) { margin-bottom: 0; }
			@include r(1140) { margin-bottom: 0; }
			@include r(1280) { margin-bottom: 0; }

			h3 {
				margin: 0;
				margin-left: 1.5rem;
				font-weight: 500;
				font-size: 1.5rem;
				line-height: 1.5;
			}
		}

		.floor-plan-meta {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			p {
				display: inline-block;
				line-height: 1.5;

				@include r(320)  { margin: 0 1.5rem 0.5rem 0; }
				@include r(480)  { margin: 0 1.5rem 0.5rem 0; }
				@include r(768)  { margin: 0 0 0 1.5rem; }
				@include r(1024) { margin: 0 0 0 1.5rem; }
				@include r(1140) { margin: 0 0 0 1.5rem; }
				@include r(1280) { margin: 0 0 0 1.5rem; }
			}

			.floor-price-value {
				color: $rh_primary_color;
			}
		}
	}

	.floor-plan-content {
		display: none;
		padding: 2rem;
		background: #fff;
		border: 1px solid rgba($rh_text_color, 0.2);
		border-top: none;

		p {
			display: block;
			margin-bottom: 2rem;
			line-height: 1.5;
		}

		img {
			display: block;
			margin: 0 auto;
		}
	}
}

.rh_property__video,
.rh_property__virtual_tour {
	display: block;
	margin-bottom: 3rem;

	.pretty-photo {
		display: block;
		position: relative;

		.play-btn {
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -24px;
			margin-left: -24px;
			width: 48px;
			height: 48px;
			background: url('../../images/play-btn.png') 50% 50% no-repeat;
			transition: 0.2s all linear;

			&:hover {
				opacity: 0.9;
				transition: 0.2s all linear;
			}
		}
	}

	iframe {
		height: 400px;
	}
}

.rh_property__map_wrap {
	display: block;
	margin-bottom: 3rem;

	#property_map {
		height: 40rem;
	}
}

.rh_property__common_note {
	display: block;
	margin-bottom: 3rem;

	p {
		padding: 2rem;
		background: $rh_body_bg;
		line-height: 2;
	}
}

.rh_property__views {
	display: block;
	padding-bottom: 3rem;
}

.rh_property__child_slider {
	@include r(320)  { margin: 0 -2rem; }
	@include r(480)  { margin: 0; }
	@include r(768)  { margin: 0; }
	@include r(1024) { margin: 0; }
	@include r(1140) { margin: 0; }
	@include r(1280) { margin: 0; }
}

.rh_property__child_properties {
	@include r(320)  { padding: 0 0 2rem 0; }
	@include r(480)  { padding: 0 0 3rem 0; }
	@include r(768)  { padding: 0 0 3rem 0; }
	@include r(1024) { padding: 0 0 3rem 0; }
	@include r(1140) { padding: 0 0 3rem 0; }
	@include r(1280) { padding: 0 0 3rem 0; }

	.flexslider {
		margin: 0;
		border: none;

		&:hover {
			ul.flex-direction-nav {
				a.flex-prev {
					left: 0;
					opacity: 1;
				}
				a.flex-next {
					right: 0;
					opacity: 1;
				}
			}
		}

		ul.slides {
			li {
				padding: 0 0 0.5rem 0;
			}
		}
	}

	ul.flex-direction-nav {
		a {
			@include r(320)  { top: 13%; }
			@include r(480)  { top: 20%; }
			@include r(768)  { top: 35%; }
			@include r(1024) { top: 35%; }
			@include r(1140) { top: 35%; }
			@include r(1280) { top: 35%; }
		}

		a.flex-prev {
			background: #fff;
			width: 40px;
			height: 60px;
			opacity: 1;
			left: 0;

			@include r(320)  { width: 30px; height: 50px; }
			@include r(480)  { width: 40px; height: 60px; }
			@include r(768)  { width: 40px; height: 60px; }
			@include r(1024) { width: 40px; height: 60px; }
			@include r(1140) { width: 40px; height: 60px; }
			@include r(1280) { width: 40px; height: 60px; }

			&:before {
				background: url(../../images/gallery-left-arrow.png) 50% 50% no-repeat;
				width: 40px;
				height: 60px;

				@include r(320)  { width: 30px; height: 50px; }
				@include r(480)  { width: 40px; height: 60px; }
				@include r(768)  { width: 40px; height: 60px; }
				@include r(1024) { width: 40px; height: 60px; }
				@include r(1140) { width: 40px; height: 60px; }
				@include r(1280) { width: 40px; height: 60px; }
			}

			&:hover {
				background: #000;

				&:before {
					background: url(../../images/gallery-left-arrow-hover.png) 50% 50% no-repeat;
				}
			}
		}
		a.flex-next {
			background: #fff;
			width: 40px;
			height: 60px;
			opacity: 1;
			right: 0;

			@include r(320)  { width: 30px; height: 50px; }
			@include r(480)  { width: 40px; height: 60px; }
			@include r(768)  { width: 40px; height: 60px; }
			@include r(1024) { width: 40px; height: 60px; }
			@include r(1140) { width: 40px; height: 60px; }
			@include r(1280) { width: 40px; height: 60px; }

			&:before {
				background: url(../../images/gallery-right-arrow.png) 50% 50% no-repeat;
				width: 40px;
				height: 60px;

				@include r(320)  { width: 30px; height: 50px; }
				@include r(480)  { width: 40px; height: 60px; }
				@include r(768)  { width: 40px; height: 60px; }
				@include r(1024) { width: 40px; height: 60px; }
				@include r(1140) { width: 40px; height: 60px; }
				@include r(1280) { width: 40px; height: 60px; }
			}

			&:hover {
				background: #000;

				&:before {
					background: url(../../images/gallery-right-arrow-hover.png) 50% 50% no-repeat;
				}
			}
		}
	}
}

.rh_property__agent_detail {
	display: block;
	border-top: 1px dashed rgba($rh_text_color, 0.2);
	margin-top: 2rem;

	@include r(320)  { padding: 2rem 0 0 0; }
	@include r(480)  { padding: 3rem 0 0 0; }
	@include r(768)  { padding: 3rem 0 0 0; }
	@include r(1024) { padding: 3rem 0 0 0; }
	@include r(1140) { padding: 3rem 0 0 0; }
	@include r(1280) { padding: 3rem 0 0 0; }
}

.rh_property__agent_head {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 1rem;

	@include r(320)  { text-align: center; }
	@include r(480)  { text-align: center; }
	@include r(768)  { text-align: left; }
	@include r(1024) { text-align: left; }
	@include r(1140) { text-align: left; }
	@include r(1280) { text-align: left; }

	figure {
		margin: 0;
		padding: 0 2rem 0 0;

		@include r(320)  { width: 100%; padding: 0 0 2rem 0; }
		@include r(480)  { width: 100%; padding: 0 0 2rem 0; }
		@include r(768)  { width: 20%; padding: 0 2rem 0 0; }
		@include r(1024) { width: 20%; padding: 0 1.5rem 0 0; }
		@include r(1140) { width: 20%; padding: 0 2rem 0 0; }
		@include r(1280) { width: 20%; padding: 0 2.5rem 0 0; }

		a {
			display: block;
			padding: 0;
		}

		img {
			width: 128px;
		}
	}

	.description {
		width: 40%;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 40%; }
		@include r(1024) { width: 40%; }
		@include r(1140) { width: 40%; }
		@include r(1280) { width: 40%; }

		.name {
			margin: 0;
			margin-bottom: 1.5rem;
			font-size: 1.9rem;
			font-weight: 500;
		}

		p {
			line-height: 1.5;

			a {
				color: $rh_heading_color;
				transition: 0.2s all linear;

				&:hover {
					color: $rh_primary_color;
					transition: 0.2s all linear;
				}
			}
		}
	}

	.contacts-list {
		width: 40%;
		padding: 0 0 0 2rem;

		@include r(320)  { width: 100%; padding: 2rem 0 0 0; }
		@include r(480)  { width: 100%; padding: 2rem 0 0 0; }
		@include r(768)  { width: 40%; padding: 0 0 0 2rem; }
		@include r(1024) { width: 40%; padding: 0 0 0 2rem; }
		@include r(1140) { width: 40%; padding: 0 0 0 2rem; }
		@include r(1280) { width: 40%; padding: 0 0 0 2rem; }

		.contact {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 1rem;
			color: $rh_heading_color;

			@include r(320)  { justify-content: center; }
			@include r(480)  { justify-content: center; }
			@include r(768)  { justify-content: flex-start; }
			@include r(1024) { justify-content: flex-start; }
			@include r(1140) { justify-content: flex-start; }
			@include r(1280) { justify-content: flex-start; }

			span {
				color: $rh_text_color;
			}
		}

		.contact.email {
			a {
				color: $rh_heading_color;
				transition: 0.2s all linear;

				&:hover {
					color: $rh_primary_color;
					transition: 0.2s all linear;
				}
			}
		}
	}
}

.rh_property__similar_properties {
	display: block;
	text-align: left;
	padding: 2rem 0 0 0;

	.rh_property__container {
