/**
 * Agent Contact Form
 */

.rh_agent_form {
	display: block;
	padding: 2rem 0 0 0;
	margin: 0;

	form {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include r(320)  { flex-direction: column; }
		@include r(480)  { flex-direction: column; }
		@include r(768)  { flex-direction: row; }
		@include r(1024) { flex-direction: row; }
		@include r(1140) { flex-direction: row; }
		@include r(1280) { flex-direction: row; }
	}

	.rh_agent_form__field {
		display: flex;
		flex-direction: column;
	}

	.rh_agent_form__text {
		width: 48%;
		margin-bottom: 1.5rem;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 48%; }
		@include r(1024) { width: 48%; }
		@include r(1140) { width: 48%; }
		@include r(1280) { width: 48%; }

		label {
			display: inline-block;
			font-size: 1.5rem;
			color: $rh_heading_color;
			margin-bottom: 1rem;
		}

		input {
			display: block;
			font-size: 1.4rem;
			color: $rh_text_color;
			border: 1px solid rgba($rh_text_color, 0.2);
			padding: 1.5rem 1rem;
			margin-bottom: 1.5rem;
			transition: 0.2s all linear;
		}

		.error {
			background: rgba(#ea3d3d, 0.05);
			border-color: #ea3d3d;
			transition: 0.2s all linear;
		}
	}

	.rh_agent_form__textarea {
		width: 100%;
		margin-bottom: 1.5rem;

		label {
			display: inline-block;
			font-size: 1.5rem;
			color: $rh_heading_color;
			margin-bottom: 1rem;
		}

		textarea {
			display: block;
			font-size: 1.4rem;
			color: $rh_text_color;
			border: 1px solid rgba($rh_text_color, 0.2);
			padding: 1.5rem 1rem;
			margin-bottom: 1.5rem;
			transition: 0.2s all linear;
		}

		.error {
			background: rgba(#ea3d3d, 0.05);
			border-color: #ea3d3d;
			transition: 0.2s all linear;
		}
	}

	.rh_agent_form__row {
		width: 100%;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		@extend p;

		#ajax-loader {
			display: none;
		}

		#error-container,
		#message-container {
			display: block;
			width: 100%;
			margin: 0;
		}

		#message-container {
			margin-top: 1rem;
		}
	}

	.inspiry-recaptcha-wrapper {
		margin-bottom: 2.5rem;
	}
}
