/**
 * Banner
 */

.rh_banner {
	display: flex;
	align-items: flex-end;
	width: 100%;
	background: $rh_banner_bg;
	position: relative;

	.rh_banner__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 0 10rem 8rem;
		margin: 0 auto;
		max-width: 1440px;

		@include r(320)  { flex-direction: column; align-items: flex-start; padding: 0 2rem 2rem; }
		@include r(480)  { flex-direction: row; align-items: center; padding: 0 3rem 3rem; }
		@include r(768)  { flex-direction: row; align-items: center; padding: 0 5rem 5rem; }
		@include r(1024) { flex-direction: row; align-items: center; padding: 0 5rem 7rem; }
		@include r(1140) { flex-direction: row; align-items: center; padding: 0 7rem 7rem; }
		@include r(1280) { flex-direction: row; align-items: center; padding: 0 10rem 8rem; }
	}

	.rh_banner__title {
		color: #fff;
		font-size: 3.6rem;
		font-weight: 400;
		margin: 0;
	}

	.rh_view_type {
		a {
			&:hover {
				path {
					fill: $rh_primary_color;
				}
			}
		}
		.active {
			path {
				fill: $rh_primary_color;
			}
		}
		svg {
			path {
				fill: #fff;
			}
		}
	}
}

.rh_banner__default {
	height: 16rem;

	@include r(320)  { height: 11rem; }
	@include r(480)  { height: 11rem; }
	@include r(768)  { height: 16rem; }
	@include r(1024) { height: 16rem; }
	@include r(1140) { height: 18.5rem; }
	@include r(1280) { height: 16rem; }
}

.rh_banner__image {
	height: 30rem;
}

.rh_banner__cover {
	display: block;
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(#000, 0.5);
}
