/**
 * Comments Template
 */

#comments {
	display: block;
	width: 100%;
	padding: 0 5rem 5rem;
	margin: 0 auto;
	background: #fff;
	text-align: left;

	@include r(320)  { padding: 0 2.5rem 0; }
	@include r(480)  { padding: 0 2.5rem 0; }
	@include r(768)  { padding: 0 5rem 0; }
	@include r(1024) { padding: 0 5rem 0; }
	@include r(1140) { padding: 0 5rem 0; }
	@include r(1280) { padding: 0 5rem 0; }

	#comments-title {
		font-size: 1.8rem;
		font-weight: 500;
		margin: 0;
		padding: 0 0 2rem 0;
		border-bottom: 1px solid rgba($rh_text_color, 0.2);

		i {
			font-size: 2.4rem;
		}
	}

	.nopassword {
		@include r(320)  { padding: 0 0 2.5rem 0; }
		@include r(480)  { padding: 0 0 2.5rem 0; }
		@include r(768)  { padding: 0 0 5rem 0; }
		@include r(1024) { padding: 0 0 5rem 0; }
		@include r(1140) { padding: 0 0 5rem 0; }
		@include r(1280) { padding: 0 0 5rem 0; }
	}
}

.commentlist {
	display: block;
	list-style-type: none;
	overflow: hidden;
    padding: 0 0 2.5rem;
    margin: 0;
    border-bottom: 1px solid rgba($rh_text_color, 0.2);

    .comment {
    	display: block;
    	margin-bottom: 2rem;
    }

    article {
    	display: flex;
    	align-items: flex-start;
    	padding: 3rem 0 2rem 0;

    	@include r(320)  { flex-direction: column; align-items: center; }
		@include r(480)  { flex-direction: row; align-items: flex-start; }
		@include r(768)  { flex-direction: row; align-items: flex-start; }
		@include r(1024) { flex-direction: row; align-items: flex-start; }
		@include r(1140) { flex-direction: row; align-items: flex-start; }
		@include r(1280) { flex-direction: row; align-items: flex-start; }

    	& > a {
    		display: inline-block;
    		min-width: 6.8rem;

    		&:hover {
    			img {
    				border: 3px solid $rh_secondary_color;
    				transition: 0.2s all linear;
    			}
    		}

    		img {
    			width: 6.8rem;
    			height: 6.8rem;
    			border-radius: 500rem;
    			border: 3px solid $rh_primary_color;
    			transition: 0.2s all linear;
    		}
    	}

    	.comment-detail-wrap {
    		padding: 0 2rem;

    		@include r(320)  { padding: 2rem 0 0; }
			@include r(480)  { padding: 0 2rem; }
			@include r(768)  { padding: 0 2rem; }
			@include r(1024) { padding: 0 2rem; }
			@include r(1140) { padding: 0 2rem; }
			@include r(1280) { padding: 0 2rem; }

    		.comment-detail-wrap-arrow {
    			display: none;
    		}

    		.comment-meta {
    			display: flex;
    			flex-wrap: wrap;

    			@include r(320)  { justify-content: center; }
				@include r(480)  { justify-content: flex-start; }
				@include r(768)  { justify-content: flex-start; }
				@include r(1024) { justify-content: flex-start; }
				@include r(1140) { justify-content: flex-start; }
				@include r(1280) { justify-content: flex-start; }

    			p {
    				margin-bottom: 0.5rem;

    				a {
    					transition: 0.2s all linear;

    					&:hover {
    						transition: 0.2s all linear;
    					}
    				}
    			}
    		}

    		.author {
    			font-weight: 500;
    			margin: 0;
    			margin-right: 0.7rem;
    			margin-bottom: 0.5rem;

    			@include r(320)  { margin-bottom: 1rem; }
				@include r(480)  { margin-bottom: 0.5rem; }
				@include r(768)  { margin-bottom: 0.5rem; }
				@include r(1024) { margin-bottom: 0.5rem; }
				@include r(1140) { margin-bottom: 0.5rem; }
				@include r(1280) { margin-bottom: 0.5rem; }
    		}

    		.url {
    			color: $rh_primary_color;
    			transition: 0.2s all linear;

				&:hover {
					color: $rh_secondary_color;
					transition: 0.2s all linear;
				}
    		}

    		.comment-body {
    			margin-bottom: 0;
    			@extend .rh_content;
    		}

    		.comment-reply-link {
    			display: inline-block;
    			color: $rh_primary_color;
    			transition: 0.2s all linear;

				&:hover {
					color: $rh_secondary_color;
					transition: 0.2s all linear;
				}
    		}
    	}
    }
}

.comment {
	.children {
		margin-left: 8.8rem;

		@include r(320)  { margin-left: 0; }
		@include r(480)  { margin-left: 0; }
		@include r(768)  { margin-left: 8.8rem; }
		@include r(1024) { margin-left: 8.8rem; }
		@include r(1140) { margin-left: 8.8rem; }
		@include r(1280) { margin-left: 8.8rem; }
	}
}

.comment-respond {

	@include r(320)  { padding: 2.5rem 0; }
	@include r(480)  { padding: 3rem 0; }
	@include r(768)  { padding: 5rem 0; }
	@include r(1024) { padding: 5rem 0; }
	@include r(1140) { padding: 5rem 0; }
	@include r(1280) { padding: 5rem 0; }

	.comment-reply-title {
		font-size: 1.8rem;
		font-weight: 500;
		margin: 0;
		margin-bottom: 1.5rem;

		small {
			margin-left: 1rem;

			a {
				color: $rh_primary_color;
				transition: 0.2s all linear;

				&:hover {
					color: darken($rh_primary_color, 5%);
					transition: 0.2s all linear;
				}
			}
		}
	}
}

.comment-form {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin: 0 -1.5rem;

	p {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		padding: 0 1.5rem 3rem 1.5rem;
		margin: 0;

		@include r(320)  { padding: 0 1.5rem 2rem 1.5rem; }
		@include r(480)  { padding: 0 1.5rem 2rem 1.5rem; }
		@include r(768)  { padding: 0 1.5rem 3rem 1.5rem; }
		@include r(1024) { padding: 0 1.5rem 3rem 1.5rem; }
		@include r(1140) { padding: 0 1.5rem 3rem 1.5rem; }
		@include r(1280) { padding: 0 1.5rem 3rem 1.5rem; }

		label {
			display: inline-block;
			color: $rh_option_active;
			margin-bottom: 1.5rem;
		}

		textarea {
			width: 100%;
			border: 1px solid rgba($rh_text_color, 0.2);
			padding: 1.5rem 2rem;
			line-height: 2;
		}
	}

	.form-submit {
		padding: 0 1.5rem;
	}

	.logged-in-as {
		flex-direction: row;
		flex-wrap: wrap;
	}

	.comment-notes {
		flex-direction: row;
		flex-wrap: wrap;

		#email-notes {
			display: inline-block;
    		margin-right: 0.5rem;
		}
	}

	.comment-form-author,
	.comment-form-email,
	.comment-form-url {
		width: 50%;

		@include r(320)  { width: 100%; }
		@include r(480)  { width: 100%; }
		@include r(768)  { width: 50%; }
		@include r(1024) { width: 50%; }
		@include r(1140) { width: 50%; }
		@include r(1280) { width: 50%; }

		input {
		    display: block;
		    width: 100%;
		    font-size: 1.4rem;
		    color: $rh_text_color;
		    margin-bottom: 1.5rem;
		    border-width: 1px;
		    border-style: solid;
		    border-color: rgba($rh_text_color, 0.2);
		    padding: 1.5rem 1rem;

		    @include r(320)  { margin-bottom: 0.5rem; }
			@include r(480)  { margin-bottom: 0.5rem; }
			@include r(768)  { margin-bottom: 1.5rem; }
			@include r(1024) { margin-bottom: 1.5rem; }
			@include r(1140) { margin-bottom: 1.5rem; }
			@include r(1280) { margin-bottom: 1.5rem; }
		}
	}

	.form-submit {
		.submit {
			@extend .rh_btn;
			@extend .rh_btn--primary;
		}
	}
}
